import constant from './constant'

export default {

    closeTisTime: '',

	historyData: [],
	recentlyDet: null,
    viewerConfig:{
        inline: true,
        title: false,
        tooltip: false,
        fullscreen: false,
        toolbar:{
            zoomIn: true,
            zoomOut: true,
            prev: true,
            next: true,
            reset: true,
            rotateLeft: true,
            rotateRight: true,
            play:false
        }
    },

    chageCloseTime(){
        this.closeTisTime = Math.floor(new Date().getTime() / 1000);
    },

	setHistoryData(v){
		this.historyData = v;
	},

	//是否用户选择
    isUserSelect(qid, aid, userSelect){

        let select = false; 

        if(!Array.isArray(userSelect)) return select;

        (userSelect || []).map(qItem => {
            if(qid == qItem.id){
                select = qItem.item.find(aItem => { return aItem.id == aid }) != null;
            }
        })
        return select;
    },

    //历史选项 ( 当前订单的最近一条有检测结果的数据 )
    isHistorySelected(qid, aid, currentObj){
 
    	if(this.recentlyDet == null && this.historyData.length != 0){

    		let index = -1;
    		for(var i = 0; i< this.historyData.length; i++){

	    		let item = this.historyData[i];

	    		if(item.detectId == currentObj.detectId){
	    			index = i;
	    		}

	    		if( (i > index && index >= 0) && 
	    			item.detectionResult != null && 
	    			item.detectionResult != ''){

	    			item.detOption = JSON.parse(item.detectionResult);

                    //必须有检测选项的才符合
                    let options = item.detOption.options;
                    if( Array.isArray(options) && options.length > 0 ){
	    			    this.recentlyDet = item;
                        break;
                    }
	    		}
	    	}
    	}

    	if(this.recentlyDet != null ){

    		let options = this.recentlyDet.detOption.options;

    		for(var i = 0; i< options.length; i++ ){
    			let temOption = options[i];
    			if(temOption.id == qid){
    				return temOption.item.find(a => { return (a.id == aid && a.selected == '1') }) != null;
    			}
    		}
    	}

    	return false;
    },

    //selected: 检测员选择色， 用户选Style: user-selected，历史记录选择Style: history-selected
	optionBtnStyle(question, answer, userSelect, currentObj){
		
		let style = '';

        if(answer.selected == '1'){
            style = ' selected ';
        }

        if(this.isUserSelect(question.id , answer.id, userSelect)){
            style += ' user-selected ';
        }


        let isHistorySelected = this.isHistorySelected(question.id , answer.id, currentObj);

        if(isHistorySelected === true){

        	style += ' history-selected ';	
        }

        return style;
	},

	//封装检测选项
	fromDetResult(obj, cid){
        
		let userSelect = JSON.parse(obj.userSelected || "[]"); 
		obj._productInfo = JSON.parse(obj.detectionInfo || 'null');
		obj._detResult = null;

        //选项分类
        obj._productInfo.options = this.dataGrouping(obj._productInfo.options, cid);

		let options = obj._productInfo.options;
        let pictures = obj._productInfo.pictures;

        if(obj.detectionResult != null){
        	let o = JSON.parse(obj.detectionResult);
            options = (Array.isArray(o.options) && o.options.length > 0) ? o.options: options;
            pictures = (Array.isArray(o.pictures) && o.pictures.length > 0) ? o.pictures: pictures;
        };

        //初始化最近的检测结果
        this.recentlyDet = null;

        //设置检测选项属性
        obj._detResult = {
            options : this.formOptions( options , userSelect , obj),
            pictures : this.formImgOption( pictures || [] )
        };

		return obj;
	},

	//封装检测选项
	formOptions(data = [], userSelect = [], currentObj = {}) {

        if (Array.isArray(data) == false)
            return [];
        let _data = [];
        try{
            (data || []).forEach(p => {
                var _p = {
                    conftype: p.conftype,
                    id: p.id,
                    name: p.name,
                    item: []
                };
                (p.item || []).forEach( a => {
                    let _a = {
                        id: a.id,
                        name: a.name,
                        priority: a.priority,
                        selected: a.selected == null ? '0' : a.selected,
                        styleAtr: this.optionBtnStyle(p, a, userSelect, currentObj)
                    }
                    _p.item.push(_a);
                });
                _data.push(_p);
            });
        }catch(er){
            console.error(er);
        }
        return _data;
    },

    formImgOption(data){
        if (Array.isArray(data) == false)
            return [];
        try{
            (data || []).map((item) => {
                item.reject = item.reject == null ? '' : item.reject;
                return item;
            })
        }catch(er){
            console.error(er);
        }
        return data;  
    },

    //格式化产品选项( 产品库的选项 )
    formattingOptions(data){

        var options = [];

        if(data == null || !Array.isArray(data)) return options;

        data.forEach( p => {
            var _p = {
                conftype: p.conftype,
                id: p.id,
                name: p.name,
                item: []
            };
            (p.question || []).forEach( a => {
                let _a = {
                    id: a.id,
                    name: a.name,
                    priority: a.priority
                }
                _p.item.push(_a);
            });
            options.push(_p);
        });
        return options;
    },

    //获取选中的答案ID
    getSelectID(data = []){
        
        let answerIds = [];

        data.forEach(item => {
            let selectItem = item.item.find(a => { return a.selected == '1' });
            if(selectItem != null){
                answerIds.push(selectItem.id);
            }
        })
        return answerIds;
    },

    compare(prop) {
        return function (obj1, obj2) {
            var val1 = obj1[prop];
            var val2 = obj2[prop];
            if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
                val1 = Number(val1);
                val2 = Number(val2);
            }
            if (val1 < val2) {
                return -1;
            } else if (val1 > val2) {
                return 1;
            } else {
                return 0;
            }            
        } 
    },

    //设置默认选项（未选择的）
    //【成色】问题项默认选择差的
    setDefaultSelect(data = [], conftype){

        if(conftype == '') return data;
        if(!Array.isArray(data)) return [];

        let taht = this;
        data.map(q =>{

            if(q.conftype == conftype){
                let temArray = q.item.sort(taht.compare("priority")),
                    goodItem = temArray[0],
                    worstItem = temArray[temArray.length-1],
                    isSelect = q.item.find( a => { return a.selected == '1' });
                (isSelect == null) && (q.item.map( a => {

                    let selectItem = goodItem;
                    if(q.name == '成色'){
                        selectItem = worstItem;
                    } 
                    if(a.id == selectItem.id){
                        a.selected = '1';
                    }
                    return a;
                }))
            }
            return q;
        });
        return data;
    },

    //根据第三方检测结果设置默认选项
    setDefaultSelectByThirdPartyInfo(data, tpData){

        if(!Array.isArray(data) || !Array.isArray(tpData)) return data;

        data.map(q => {
            let t = tpData.find( item => { return item.option == q.name });
            if(t != null){
                q.item.map( a => {
                    if(a.name == t.val){
                        a.selected = '1';
                    }
                    return a;
                })
            }
            return q;
        });
        return data;
    },


    //获取需要检测的产品选项
    getUnselected(data){
        let list = [];

        data.forEach(item => {
            
            let isUnSelect = item.item.find( a => { return a.selected == '1' }) == null;

            if(isUnSelect){
                list.push(item);
            }

        });

        return list;
    },

    //选项映射
    mappingOptions(data) {

        let list = [];
        if (data != null) {
            if (data.color != null) {
                list.push({
                    option: '颜色',
                    val: data.color
                });
            }
            if (data.repairsLeftDays != null) {
                let val = "";
                if (data.repairsLeftDays != "") {
                    val =
                        data.repairsLeftDays > 30 ?
                        "保修一个月以上" :
                        "保修一个月以下或过保";
                }
                list.push({
                    option: '保修期',
                    val: val
                });
            }
            if (data.activationLock != null) {
                let val = "";
                if (data.activationLock == "OFF") {
                    val = "iCloud已注销";
                }
                if (data.activationLock == "ON") {
                    val = "iCloud无法注销";
                }
                list.push({
                    option: 'iCloud',
                    val: val
                });
            }
            if (data.replacedDo != null) {
                let val = "";
                if (data.replacedDo == "YES") {
                    val = "官换机";
                }
                if (data.replacedDo == "NO") {
                    val = "非官换机";
                }
                list.push({
                    option: '官换机',
                    val: val
                });
            }
            if (data.capacity != null) {
                let val = data.capacity;
                list.push({
                    option: '存储容量',
                    val: val
                });
            }
        }
        return list;
    },

    //获取翻译所需图片
    translatePic(picList, cid){

        if(!Number.isInteger(cid)  == null) return picList;

        let _data = [],
            _types = [];

        if(cid == 3){
            _types = [
                constant.IMG_TYPE.IMEI,
                constant.IMG_TYPE.MODEL,
                constant.IMG_TYPE.STORAGE,
                constant.IMG_TYPE.RUN_STORAGE,
                constant.IMG_TYPE.REVERSE
            ];
        }

        if(_types.length == 0) return picList;

        _data = picList.filter(t => {  return (_types.indexOf(+t.type) != -1 ) });

        return _data;
    },

    //获取检测所需图片
    detPic(picList, cid){

        if(!Number.isInteger(cid)  == null) return picList;

        let _data = [],
            _types = [];

        if(cid == 3){
            _types = [
                constant.IMG_TYPE.FRONT,
                constant.IMG_TYPE.REVERSE,
                constant.IMG_TYPE.DOWNSIDE,
                constant.IMG_TYPE.WHITE
            ];
        }

        if(_types.length == 0) return picList;

        _data = picList.filter(t => {  return (_types.indexOf(+t.type) != -1 ) });

        return _data;
    },

    //选项分类
    // type: 1 图片翻译能选出的问题项
    //       2 机况识别能选出的问题项
    //       3 目前云检测无法失败的问题项目
    dataGrouping(data, cid){
        
        if(!Array.isArray(data)) return [];

        data.map(item => {
            constant.OPTION_GROUP.forEach(g => {
                if(g.OPTIONS.indexOf(item.name) != -1){
                    item.conftype = g.GROUP;
                }
            });
            return item;
        })
        return data;
    }
}


