<!-- 检测详细信息 -->
<template>
    <section>
        <ul_det_info :data="data" :opType="2" ></ul_det_info>
        <el-row type="flex" justify="center">
            <el-col :span="4">
                <router-link to="/DetectRecord">
                    <el-button style="width:150px;">关闭</el-button>
                </router-link>
            </el-col>
        </el-row>
    </section>
</template>

<script type="text/javascript">

    import detDetails from '../../component/detDetails'
    import detCommon from '../../common/js/detCommon'
    import { getDetectRecordDetail } from '../../api/api'

    export default {
        data(){
            return {
                detectId: '',
                data: []
            }
        },
        mounted(){
            this.detectId = this.$route.query.detectId;
            this.loadData();
        },
        methods:{
           loadData(){
                getDetectRecordDetail({detectId: this.detectId}).then(res => {
                    if(res.status){
                        let info = detCommon.fromDetResult(res.data);
                        this.data = [info];
                    }else{
                        this.$message.error(res.retinfo);
                    }
                });
           }
        },
        components:{
            'ul_det_info': detDetails
        }
    }
</script>




