import constant from './constant'
import util from './util'



export default {

    //移除登陆信息
    removeLoginInfo(){
        this.removeSession('menu');
        this.removeSession('user_id');
        this.removeSession('userInfo');
        this.removeSession('token');
    },

    removeSession(key) {
        localStorage.removeItem(key);
        //sessionStorage.removeItem(key);
    },

    setSession(key, val) {
        localStorage.setItem(key, val);
        //sessionStorage.setItem(key, val);
    },

    getSession(key) {
        return localStorage.getItem(key);
        //return sessionStorage.getItem(key);
    },

    getLoginUserMenu(){
        return JSON.parse(this.getSession('menu'));
    },

    //设置用户所拥有的权限
    setLoginUserMenu(val) {
        this.setSession('menu', val);
    },

    //设置用户ID
    setLoginUserId(val) {
        this.setSession('user_id', val);
    },

    //设置登陆用户信息
    setLoginUserInfo(val) {
        this.setSession('userInfo', val);
    },

    //设置用户登陆Token
    setLoginUserToken(val) {
        this.setSession('token', val);
    },

    //获取当前登陆用户id
    getUserId() {
        return this.getSession('user_id');
    },

    //获取当前登陆用户信息
    getUserInfo() {
        return JSON.parse(this.getSession('userInfo'));
    },

    //获取登陆用户令牌
    getToken() {
        return this.getSession('token');
    },

    formatDay(v) {
        if (v < 10) {
            return '0' + v;
        }
        return '' + v;
    }, 

    //获取两个区间的时间
    getIntervalTime(day){
        let inDay = 86400000 * day;
        let endTime = new Date();
        let currentTime = new Date(endTime.getTime() - inDay);

        return {
            startTime: currentTime,// util.formatDate( currentTime , 'yyyy-MM-dd'),
            endTime: endTime //util.formatDate( endTime , 'yyyy-MM-dd')
        };
    },
    showErrorMsg(that, message) {
        that.$message.error({
            showClose: false,
            message
        });
    },
    showWarningMsg(that, message) {
        that.$message.warning({
            showClose: false,
            message
        });
    },
    deepCloneByJson(obj) {
        // 仅用于可JSON化的对象，属性值含undefined或function的不可用！
        return JSON.parse(JSON.stringify(obj));
    },

    formGeneralObj(val,data,key,lable){
        if(val == '' || data == null || key =='') return val;

        let rs = val;

        (data || []).forEach(function(item){
            if(item[key] == val){
                rs = lable == '' ? item: item[lable];
                return;
            }
        });
        return rs;
    },

    getStatusStr(v){
        return this.formGeneralObj(v, constant.STATUS,'value','label');
    },

    getImgStr(v){
        return this.formGeneralObj(v, constant.IMG_TYPE_NAME,'value','label');  
    },

    secondFormat(val){
        let s = val % 60,
            m = Math.floor(val / 60 % 60),
            h = Math.floor(val / 60 / 60 % 24),
            d = Math.floor(val / 60 / 60 / 24);

        let str = s + '秒';
            str = ( m > 0 ? (m + '分') : '' ) + str;
            str = ( h > 0 ? (h + '时') : '' ) + str;
            str = ( d > 0 ? (d + '天') : '' ) + str;
        return str;
    },  


    formDetOption(data, userOptions){

        let recentlyOption = {};

        data.forEach(item => {
            if(item.detectionResult != null){
                recentlyOption = item.detectionResult;
            }
        });



    },
}


