<template>
    <section>
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters" ref="queryForm" :rules="queryFormRules" >
                <el-form-item label="生成时间" required>
                    <el-form-item prop="startTime">
                        <el-date-picker
                            size="small"
                            v-model="filters.startTime"
                            type="date"
                            format="yyyy-MM-dd"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>--
                    <el-form-item prop="endTime">
                        <el-date-picker
                            size="small"
                            v-model="filters.endTime"
                            type="date"
                            format="yyyy-MM-dd"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select size="small" v-model="filters.status" multiple clearable placeholder="请选择">
                        <el-option
                          v-for="item in orderStatus"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="接入方">
                    <el-select size="small" v-model="filters.accessType" multiple clearable placeholder="请选择">
                        <el-option label="换机侠" value="1"></el-option>
                        <el-option label="ATM" value="2"></el-option>
                        <el-option label="TOC" value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="搜索">
                    <el-input size="small" v-model="filters.search" placeholder="接入ID/检测单ID/工单ID/IMEI" @click.native="clearSearch" @keyup.enter.native="getData" clearable ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size="small" :plain="true" type="primary" v-on:click="handleCurrentChange(1)">搜索</el-button>
                    <el-button size="small" @click="download" v-loading="dowIsLoad">导出Excel</el-button>
                    <el-button size="small" @click="showDownloadList">查看导出列表</el-button>
                </el-form-item>
            </el-form>
            <el-dialog
                    title="导出列表"
                    :visible.sync="dialogVisible"
                    width="550px">
                <p v-for="item in downloadFiles" style="display: flex; justify-content: space-between; padding: 0 20px;">
                    <a :href="hostUrl + '/' + item.url" target="_blank">{{item.url}}</a>
                    <span>{{item.time}}</span>
                </p>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">确 定</el-button>
                </span>
            </el-dialog>

        </el-col>

        <el-table border :data="datalist" highlight-current-row v-loading="listLoading" >
            <el-table-column type="index" label="序号" width="50"></el-table-column>
            <el-table-column prop="Fdetect_id" label="检测ID" ></el-table-column>
            <el-table-column prop="Faccess_name" label="接入方" ></el-table-column>
            <el-table-column prop="Ftype_name" label="接入ID类型" ></el-table-column>
            <el-table-column prop="Funique_id" label="接入ID" ></el-table-column>
            <el-table-column prop="Fproduct_name" label="型号" ></el-table-column>
            <el-table-column prop="Fimei" label="IMEI" ></el-table-column>
            <el-table-column prop="Fcreate_time" label="生成时间" width="150"></el-table-column>
            <el-table-column prop="Ffinish_time" label="完成时间" width="150"></el-table-column>
            <el-table-column prop="Fwatting_time" label="等待时长" :formatter="formatterWaitingTime"></el-table-column>
            <el-table-column prop="Fduring_time" label="处理时长" :formatter="formatterDuringTime"></el-table-column>
            <el-table-column prop="Fstatus" label="当前状态" :formatter="formatterStatus"></el-table-column>
            <el-table-column prop="Finspector" label="处理人" ></el-table-column>

            <el-table-column label="操作" width="200">
                <template slot-scope="scope">
                    <router-link target="_blank" :to="{ path: '/CloudsDetectDetail', query: { detectId: scope.row.Fdetect_id }}">查看</router-link>
                </template>
            </el-table-column>
        </el-table>

        <el-col :span="24" class="toolbar">
            <el-pagination layout="total, prev, pager, next" @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>

    import util from '../../common/js/util'
    import constant from '../../common/js/constant'
    import common from '../../common/js/common'
    import { getDetectRecord, getDetectRecordDetail, exportDetectRecord } from '../../api/api'

    export default {
        data(){
            return {
                orderStatus: constant.STATUS,

                queryFormRules:{
                    startTime: [ { type: 'date', required: true, message: '请选择日期', trigger: 'change' } ],
                    endTime: [ { type: 'date', required: true, message: '请选择时间', trigger: 'change' } ],
                },
                filters:{
                    startTime: '',
                    endTime: '',
                    search:'',
                    status: []
                },
                datalist:[],
                total: 0,
                page: 1,
                pageSize:constant.PAGESIZE,

                listLoading:false,
                dowIsLoad:false,
                dialogVisible: false,
                downloadFiles: [],
                hostUrl: constant.HostUrl,

                detectionVisible:false,
                detectID:0,
                currentTime:0
            }
        },
        mounted(){
            let inTime = common.getIntervalTime(7);
            this.filters.startTime = inTime.startTime;
            this.filters.endTime = inTime.endTime;
            this.getData();
        },
        methods:{
            
            pageTo(path, queryObj = {}) {
                this.$router.push({ path: path, query: queryObj });
            },

            fmResults(obj){
                return common.fromDetectResults(obj.detectResults);
            },

            formatterStatus(obj){
                let val = '--';
                this.orderStatus.map(function (item, index) {
                    if (item.value == obj.Fstatus) {
                        val = item.label;
                        return false;
                    }
                });
                return val;
            },

            formatterWaitingTime(obj) {
                let differTime = 0;
                if (obj.Fstatus == 1) {
                    let time = Math.floor(new Date(obj.Fcreate_time).getTime() / 1000);
                    differTime = this.currentTime - time;
                    differTime = Math.floor(differTime);
                } else {
                    differTime = Math.floor(obj.Fwatting_time);
                }
                return util.MillisecondToTime( differTime);
            },

            formatterDuringTime(obj) {
                if (obj.Fstatus == 1) {
                    return '';
                } else {
                    return util.MillisecondToTime( obj.Fduring_time);
                }
            },

            clearSearch(){
                this.filters.search = '';
            },

            _getPara(){
                let para = Object.assign({},this.filters);
                para.startTime = util.formatDate( para.startTime , 'yyyy-MM-dd') + ' 00:00:01';
                para.endTime = util.formatDate( para.endTime , 'yyyy-MM-dd') + ' 23:59:59';

                return para;
            },

            getData(){
                this.$refs.queryForm.validate((valid) => {
                    if (valid) {
                        let para = this._getPara();
                        if (para.startTime > para.endTime) {
                          this.$message.warning('开始时间不能大于结束时间');
                          return false;
                        }
                        para.pageIndex = this.page - 1;
                        para.pageSize = this.pageSize;
                        para.searchType = para.status.join(',');
                        para.accessType = para.accessType.join(',');

                        this.listLoading = true;
                        this.datalist = [];
                        getDetectRecord(para).then((res) => {

                            this.listLoading = false;
                            if(res.status){
                                this.datalist = res.data.detectRecordList;
                                this.total = parseInt(res.data.pageInfo.total || 0,10);
                                this.currentTime = res.data.nowTime;
                            }else{
                                this.total = 0;
                                this.$message.error('读取数据失败：' + res.retinfo);
                            }
                        },(er) => {
                            this.listLoading = false;
                            console.log(er);
                        });
                    }
                });

            },
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            detectInfo(obj){
                let detectId = parseInt(obj.Fdetect_id,10) || 0;

                if (!detectId) {
                    this.$message.error('系统出错，请重试!');
                    return false;
                }

                this.pageTo('/CloudsDetectDetail', { detectId: detectId });

            },
            download(){
                this.$refs.queryForm.validate((valid) => {
                    if (valid) {
                        let para = this._getPara();
                        if (para.startTime > para.endTime) {
                            this.$message.warning('开始时间不能大于结束时间');
                            return false;
                        }
                        para.searchType = para.status.join(',');

                        this.dowIsLoad = true;
                        exportDetectRecord(para).then(res =>{
                            if(res.status){ 
                                let time = util.formatDate(new Date(),'yyyy-MM-dd hh:mm:ss');
                                let dStr = res.data + '#' + time;
                                this.setDownloadFiles(dStr)
                            }else{
                                this.$message.error('下载失败：' + res.retinfo);
                            }
                            this.dowIsLoad = false;
                        });
                    }
                });
            },

            setDownloadFiles(val){
                let str = decodeURIComponent(util.getCookie('downloadFiles')),
                    ds = [];

                if(str != '')
                    ds = str.split(',');

                ds.unshift(val);

                if(ds.length > 10){
                    ds.pop();
                }
                util.setCookie('downloadFiles',decodeURIComponent(ds.join(',')));
            },

            // 获取 cookie 中下载文件列表
            getDownloadFiles() {
              
                let downloadFiles = decodeURIComponent(util.getCookie('downloadFiles')),
                    downloadFilesList = [];

                downloadFiles = downloadFiles.split(',');
                downloadFiles.map(function (item, i) {
                   
                    let time =  item.split('#')[1];
                    downloadFilesList.push({ url: item.split('#')[0], time:time });
                });

                return downloadFilesList;
            },
            // 显示下载文件列表
            showDownloadList() {

                let downloadFiles = this.getDownloadFiles();

                this.downloadFiles = downloadFiles || [];

                if (this.downloadFiles.length) {
                    this.dialogVisible = true;
                } else {
                    this.$message('请先点击“导出Excel”');
                    this.dialogVisible = false;
                }
            },
            digVisible(v){
                this.detectionVisible = v;
            }
        },
        components:{
        },
        watch:{
            '$route'(to, from){

            }
        }
    }
</script>



