import axios from 'axios';
import util from '../common/js/util';
import constant from '../common/js/constant';
import common from '../common/js/common';

//开放权限验证的接口
const openApi = ['authlogin', 'authregistersystem'];

//权限中心API
const AMC_API = ['authlogin', 'authregistersystem', 'logininfo', 'systemusers', 'editpassword'];

//const AMC_API_BASE = '/amc_api';
const AMC_API_BASE =  constant.AMC_Url;

//OMS API
const OMS_API_BASE = constant.HostUrl;

let getAPIUrl = (apiName, systemID) => {

    let api_url = OMS_API_BASE;

    if (AMC_API.includes(apiName)) {
        api_url = AMC_API_BASE;
    }
    return api_url;
}

//发送请求
let sendHttp = (apiName, apiUrl, params) => {

    let requestParams = {
        head: constant.paramsHead,
        params: {}
    };
    requestParams.head.interface = apiName;

    let url = getAPIUrl(apiName) + apiUrl;
    //排除登陆接口
    if (!openApi.includes(apiName)) {
        let user_id = common.getUserId();
        let token = common.getToken();
        if (!user_id || !token) {
            console.log('登陆超时');
            return false;
        }

        //添加公共参数
        params.login_user_id = user_id;
        params.login_token = token;
        params.login_system_id = constant.CURRENT_SYSTEM_ID;
    }
    params.system = 'upin';
    params.time = new Date().getTime().toString().substr(0, 10);
    requestParams.params = params;

    // if (apiName == 'downloadRecord') { // 获取 excel 请求
    //     return axios.post(url, requestParams, {responseType: 'arraybuffer'}).then(res => res);
    // }
    return axios.post(url, requestParams).then(res => res);
}

//结果处理
let res_factory = res => {
    // 返回 excel 流文件，直接返回
    // if (res.headers['content-type'] == 'application/vnd.ms-excel') {
    //     return res;
    // }
    if (res.status != 200 || res.data.body == null) {
        return {
            status: false,
            retcode: '',
            retinfo: '系统异常'
        }
    };
    let data = constant.RES_ARRAY,
        resBody = res.data.body;
    //默认是错误的处理方式
    resBody.status = false;
    data.map(function(item, index) {
        if (item.retcode == resBody.retcode) {
            resBody.status = item.status;
            resBody.retinfo = item.retinfo;
        }
    });

    if (resBody.retcode == '10006') {
        window.location.href = "/#/login";
    }
    return resBody;
}


//请求前处理
axios.interceptors.request.use(function(config) {
    return config;
}, function(err) {
    console.log('request errod', err);
    return Promise.reject(err);
});

//响应结果处理
axios.interceptors.response.use(function(res) {
    res = res_factory(res);
    return res;
}, function(err) {
    console.error('response error', err);
    return Promise.reject(err);
});

/* 公共 API */
const requestLogin = params => {
    return sendHttp('authlogin', '/authlogin', params);
};
const regSystem = params => {
    return sendHttp('authregistersystem', '/authregistersystem', params);
};
const getLoginUser = params => {
    return sendHttp('logininfo', '/logininfo', params);
};

export const login = params => {

    //系统标识符
    params.system_id = constant.CURRENT_SYSTEM_ID;

    return requestLogin(params).then(data => {
        if (!data.status) {
            return data;
        }
        common.setLoginUserId(data.data.user_id);
        common.setLoginUserToken(data.data.login_token);
        let params = {
            token: data.data.login_token,
            user_id: data.data.user_id,
            system_id: constant.CURRENT_SYSTEM_ID
        }
        return regSystem(params);
    }).then(data => {
        if (!data.status) {
            return data;
        }
        let params = {};
        return getLoginUser(params);
    });
}


