<template>
    <div>
        <el-container>
            <el-aside width="200px">
                <el-table
                        :data="detectList"
                        :sort="true"
                        :default-sort = "{prop: 'detectId', order: 'ascending'}">
                    <el-table-column prop="detectId" label="工单ID"></el-table-column>
                    <el-table-column label="等待时长">
                        <template slot-scope="scope">
                            <span :class="scope.row.urgency ? 'urgency' : ''">{{scope.row.differTime}}</span>
                        </template>
                    </el-table-column>
                </el-table>
                <p class="p-txt-c">
                    共<strong class="urgency">{{queueTotal}}</strong>单
                </p>
            </el-aside>
            <el-main>
                <el-row :gutter="24">
                    <el-col :span="10" :offset="5">
                        <div>
                            <h1 style="font-size: 1.5rem">今日已检测 <span style="color: red;">{{count}}</span> 单</h1>
                        </div>
                    </el-col>
                </el-row>
                <el-row :gutter="24">
                    <el-col :span="10" :offset="5">
                        <div>
                            <el-button icon="el-icon-caret-right" style="font-size: 2rem" @click="actionDetect">开始检测...</el-button>
                            <p style="margin-top: 2rem">开始检测后，系统会自动派单。</p>
                            <p>若停止检测，请点击右上角【退出检测】</p>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </div>
</template>

<script>

    import util from '../../common/js/util'
    import constant from '../../common/js/constant'
    import detCommon from '../../common/js/detCommon'
    import workOrderNotify from '../../component/workOrderNotify'
    import { getWaitingDetectOrder,actionDetectStatus } from '../../api/api'

    export default {
        data(){
            return {
                detectList: [],
                count: 0,
                queueTotal: 0,
                listLoading: false,
                waitDetectOrderInfo:{
                    detect: 0,
                    interpreter: 0
                },
                opNode: '',
                notify: {}
            }
        },
        mounted(){
            this.opNode = '';
            this.getData();
            this.setIntervalReflush();
        },
        beforeDestroy(){
            this.opNode = 'route';
            this.notify.close && this.notify.close();
            this.clearIntervalReflush();
        },
        methods:{

            setIntervalReflush() {
                var that = this;
                if (window._dt_task_id) {
                    window.clearInterval(window._dt_task_id);
                }
                window._dt_task_id = setInterval(function(){
                    that.getData();
                }, 3000);
            },

            clearIntervalReflush() {
                clearInterval(window._dt_task_id);
            },

            pageTo(path, queryObj = {}) {
                this.$router.push({ path: path, query: queryObj });
            },

            getData() {
                getWaitingDetectOrder({}).then((res) => {
                    this.listLoading = false;
                    if(res.status){

                        let data = res.data.list,
                            currentTime = parseInt(res.data.nowTime,10);

                        data.map(item => {
                            let itemTime = Math.floor(new Date(item.createTime).getTime()) / 1000;
                            let temTime = Math.floor(currentTime - itemTime);

                            item.urgency = temTime > 10;
                            item.differTime = util.MillisecondToTime( temTime ); 
                        });

                        this.detectList = data; 
                        this.waitDetectOrderInfo = res.data.waitDetectOrderInfo;

                        this.count = res.data.count;
                        this.queueTotal = res.data.total;
                    }else{
                        this.$message.error('获取数据失败，请刷新重试。' + res.retinfo);
                    }
                },(er) => {
                    this.listLoading = false;
                    console.log(er);
                });
            },

            // 开始检测
            actionDetect() {
                if (this.detectList.length < 1) {
                    this.$message.warning('暂无检测单，休息一下吧！');
                    return ;
                }
                this.pageTo('/CloudsDetection');
            }
        },
        components:{
        },
        watch:{
            'waitDetectOrderInfo.interpreter'(v){

                if(v > 0){
                    let that = this,
                        currentTime = Math.floor(new Date().getTime() / 1000),
                        differTime = currentTime - detCommon.closeTisTime;
                    
                    if( detCommon.closeTisTime == 0 || 
                        differTime >= constant.TISTIME ){

                        if(this.notify.visible == null || this.notify.visible == false){
                            this.notify = this.$notify({
                              title: '提示',
                              iconClass: 'el-icon-bell',
                              message: '<strong style="color:red;" >有新的翻译单</strong>',
                              dangerouslyUseHTMLString: true,
                              duration: 0,
                              onClose:function(){
                                if(that.opNode == '')
                                    detCommon.chageCloseTime();
                              }
                            });
                        }
                    }
                }

                if(v <= 0){
                    this.opNode = 'route';
                    this.notify.close && this.notify.close();
                }
            }
        }
    }
</script>

<style>
    .urgency{
        color: red;
        font-weight: bold;
    }
    .p-txt-c{
        text-align: center;
        line-height: 30px;
    }
</style>



