
const IMG_TYPE = {
    FRONT: 1,
    REVERSE: 2,
    DOWNSIDE: 3,
    WHITE: 4,
    IMEI: 5,
    MODEL: 6,
    STORAGE: 7,
    RUN_STORAGE: 8,
    FRONT_WHITE: 9,
    APPEARANCE: 10,
    SCREEN_5: 11,
    SCREEN_10: 12,
    SCREEN_20: 13,
    SCREEN_DARK_40: 14,
    SCREEN_DARK_50: 15
};

const OPTION_TYPE = {
    SKU: 1,
    CDT: 2,
    FUN: 3
}


export default {

    //分页 页大小
    PAGESIZE: 10,

    //列表查询时间默认间隔天数
    INTERVAL_DAY:7,

    //当前系统标识符 开发环境：106，测试环境：101
    CURRENT_SYSTEM_ID: process.env.CURRENT_SYSTEM_ID,

    //请求头
    paramsHead: {
        msgtype: "request",
        remark: "",
        version: "0.01"
    },

    //接口结果转换
    RES_ARRAY: [
        { retcode: '0', status: true, retinfo: '正确' }
    ],

    HostUrl: process.env.DETECT_API,

    AMC_Url: process.env.AMC_API,

    //心跳包间隔时间 (毫秒)
    BEATTIM: 1000,

    //停止工作有效时间 (秒)
    VALID_TIME: 30,

    //提示再次开启时间（秒）
    TISTIME:  3600,

    //状态
    STATUS:[{
        value: '1',
        label: '待检测'
    },{
        value: '2',
        label: '检测中'
    },{
        value: '3',
        label: '已完成'
    },{
        value: '4',
        label: '图片驳回'
    },{
        value: '5',
        label: '已关闭'
    },{
        value: '10',
        label: '待翻译'
    },{
        value: '20',
        label: '翻译中'
    },{
        value: '30',
        label: '翻译完成'
    },{
        value: '40',
        label: '翻译驳回'
    },{
        value: '50',
        label: '翻译关闭'
    }],

    //手机系统
    PHONE_SYSTEM:{
        IOS: '2',
        Android: '1'
    },

    //选项类型 
    // type: 1 图片翻译能选出的问题项
    //       2 机况识别能选出的问题项
    //       3 目前云检测无法失败的问题项目
    OPTION_TYPE: OPTION_TYPE, 

    //tem选项组 临时处理方法
    OPTION_GROUP:[{
        GROUP: OPTION_TYPE.CDT,
        OPTIONS: ['进水','机身']
    },{
        GROUP: OPTION_TYPE.SKU,
        OPTIONS: ['iCloud']
    }],

    //驳回类型 1: 图片驳回, 2: 暂无机型驳回
    REJECT_TYPE:{
        IMG: 1,
        NOT_PRODUCT: 2  
    },

    IMG_TYPE: IMG_TYPE,

    IMG_TYPE_NAME:[
        {
            value: IMG_TYPE.FRONT,
            label: '正面'
        },{
            value: IMG_TYPE.REVERSE,
            label: '背面'
        },{
            value: IMG_TYPE.DOWNSIDE,
            label: '下侧面'
        },{
            value: IMG_TYPE.WHITE,
            label: '白底液晶'
        },{
            value: IMG_TYPE.IMEI,
            label: 'IMEI'
        },{
            value: IMG_TYPE.MODEL,
            label: '型号'
        },{
            value: IMG_TYPE.STORAGE,
            label: '内存'
        },{
            value: IMG_TYPE.RUN_STORAGE,
            label: '运行内存'
        },{
            value: IMG_TYPE.FRONT_WHITE,
            label: '正面白屏'
        },{
            value: IMG_TYPE.APPEARANCE,
            label: '外观'
        },{
            value: IMG_TYPE.SCREEN_5,
            label: '亮屏1'
        },{
            value: IMG_TYPE.SCREEN_10,
            label: '亮屏2'
        },{
            value: IMG_TYPE.SCREEN_20,
            label: '亮屏3'
        },{
            value: IMG_TYPE.SCREEN_DARK_40,
            label: '暗屏1'
        },{
            value: IMG_TYPE.SCREEN_DARK_50,
            label: '暗屏2'
        }
    ]

}


