<template>
    <div>

        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-row>
                <el-col :span="20">
                    <el-form :inline="true" :model="filters" ref="queryForm">
                        <el-form-item label="状态">
                            <el-select size="small" v-model="filters.status" clearable placeholder="请选择">
                                <!--<el-option key="" label="全部" value="" ></el-option>-->
                                <el-option
                                        v-for="item in status"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button size="small" :plain="true" type="primary" v-on:click="getData">筛选</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-col :span="4">
                    <el-button @click="accessConfigInfo()">新增接入</el-button>
                </el-col>
            </el-row>
        </el-col>

        <template>
            <el-table :data="datalist" border>
                <el-table-column prop="accessId" label="接入ID" width=""></el-table-column>
                <el-table-column prop="accessName" label="接入方名称" width=""></el-table-column>
                <el-table-column prop="desc" label="描述" width=""></el-table-column>
                <el-table-column prop="typeId" label="接入ID类型" :formatter="formatTypeVal" width=""></el-table-column>
                <el-table-column prop="status" label="状态" :formatter="formatStatus" width=""></el-table-column>

                <el-table-column label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="accessConfigInfo(scope.row, 'info')">查看</el-button>
                        <el-button type="text" size="small" @click="accessConfigInfo(scope.row, 'edit')">编辑</el-button>
                        <el-button type="text" size="small" @click="confirmActionStatus(scope.row)">{{scope.row.status == '1' ? '禁用' : '启用'}}</el-button>
                    </template>
                </el-table-column>

            </el-table>
        </template>

        <el-col :span="24" class="toolbar">
            <el-pagination layout="total, prev, pager, next" @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
            </el-pagination>
        </el-col>

        <el-dialog title="配置详情" width="80%" :visible.sync="configureVisible" >
            <configure_ul ref="configureInfo" v-on:notice='digVisible' :actionType="actionType" :accessId="accessId" ></configure_ul>
        </el-dialog>
    </div>
</template>

<script>

    import constant from '../../common/js/constant'
    import configure from '../../component/configure'
    import { getAccessConfigList,setAccessConfigStatus } from '../../api/api'

    export default {
        data(){
            return {

                filters:{
                    status:''
                },
                status: [{label:'启用', value:'1'},{label:'禁用', value:'2'}],

                datalist:[],
                total: 0,
                page: 1,
                pageSize:constant.PAGESIZE,

                configureVisible: false,
                accessId: 0,
                actionType: '',

                listLoading: false,
            }
        },
        mounted(){
            this.getData();
        },
        created(){
        },
        methods:{
            pageTo(path, queryObj = {}) {
                this.$router.push({ path: path, query: queryObj });
            },
            digVisible(v){
                this.configureVisible = v;
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            confirmActionStatus( row ) {
                let action = row.status == '1' ? '禁用' : '启用';
                if (!row.accessId || !row.status) {
                    this.$message({
                        message: '无法获取接入ID或当前状态',
                        type: 'error'
                    });
                    return false;
                } else {
                    this.$confirm(action + '当前配置，确定继续吗？', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.handleStatus( row );
                    }).catch(() => {});
                }
            },
            handleStatus( row ) {
                let status = row.status == '1' ? 2 : 1;
                let para = { accessId: row.accessId, status: status };

                setAccessConfigStatus(para).then((res) => {
                    if(res.status){
                        this.$message.success('操作成功');
                    }else{
                        this.$message.error('操作失败');
                    }
                    this.getData();
                },(er) => {
                    this.listLoading = false;
                    console.log(er);
                });

            },
            getData() {
                let para = Object.assign({}, this.filters);

                para.pageIndex = this.page - 1;
                para.pageSize = this.pageSize;

                this.listLoading = true;
                this.datalist = [];
                getAccessConfigList(para).then((res) => {

                    this.listLoading = false;
                    if(res.status){
                        this.datalist = res.data.accessConfigList;
                        this.total = parseInt(res.data.pageInfo.total || 0,10);
                    }else{
                        this.total = 0;
                        this.$message.error('读取数据失败：' + res.retinfo);
                    }
                },(er) => {
                    this.listLoading = false;
                    console.log(er);
                });
            },
            formatStatus(obj){
                let val = '--';
                this.status.map(function(item, key){
                    if (item.value == obj.status) {
                        val = item.label;
                        return ;
                    }
                });
                return val;
            },
            formatTypeVal(obj){
                let val = '--';
                switch (obj.typeId)
                {
                    case '1':
                        val = '订单号';
                        break;
                    case '2':
                        val = '映射号';
                        break;
                }
                return val;
            },
            accessConfigInfo( row = '', type = 'add' ) {
                this.actionType = type;
                switch (type) {
                    case 'add':
                        this.configureVisible = true;
                        this.$refs.configureInfo.initForm();
                        break;
                    case 'edit':
                        this.configureVisible = true;
                        this.accessId = parseInt(row.accessId || 0);
                        break;
                    case 'info':
                        this.configureVisible = true;
                        this.accessId = parseInt(row.accessId || 0);
                        break;
                    default:
                        console.log("something going wrong");
                        break;
                }
            },
        },
        components:{
            'configure_ul':configure
        },
        watch:{
        }
    }
</script>


