//校验

export default {

    //是否包含特殊字符串
    specialString(str) {

        var regEn = /[`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/im,
            regCn = /[·！#￥（——）：；“”‘、，|《。》？、【】[\]]/im;

        if (regEn.test(str) || regCn.test(str)) {
            return false;
        }
        return true;
    },

    //ime验证(只验证长度15)
    imei(val) {

        return this.specialString(val) && (val || '').length == 15;
    },

    limitLength(len) {
        return function (rule, value, callback) {
            // let length = 0;
            // for (let i = 0; i < value.length; i++) {
            //     let charCode = value.charCodeAt(i);
            //     if (charCode >= 19968 && charCode <= 40869) {
            //         length += 2;
            //     } else {
            //         length += 1;
            //     }
            // }
            if (value.length > len) {
                return callback(new Error('字符长度不可超过' + len /*+ '（中文按2个字符计）'*/));
            }
            return callback();
        };
    } 
}


