export default {

    getQueryStringByName: function(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        var context = "";
        if (r != null)
            context = r[2];
        reg = null;
        r = null;
        return context == null || context == "" || context == "undefined" ? "" : context;
    },
    conversion_trre: function(data) {

        if (data == null || data.length == 0) return data;
    },
    setCookie: function(name, value, expires, path, domain, secure) {
        var curCookie = name + "=" + escape(value) +
            ((expires) ? "; expires=" + expires.toGMTString() : "") +
            ((path) ? "; path=" + path : "") +
            ((domain) ? "; domain=" + domain : "") +
            ((secure) ? "; secure" : "");
        document.cookie = curCookie;
    },
    getCookie: function(name) {
        var r = new RegExp("(?:^|;+|\\s+)" + name + "=([^;]*)"),
            m = document.cookie.match(r);
        return (!m ? "" : m[1]);
    },
    removeEmptyAtr: function(obj) {
        if (obj == null) return null;
        for (let a in obj) {
            if (obj[a] == null || obj[a] == '') {
                delete obj[a];
            }
        }
        return obj;
    },

    //分转元
    formatMoney: function(money) {
        if (typeof money != 'number') {
            return money;
        }
        var rs = money / 100;
        return rs;
    },

    //元转分
    formatMoneyFen: function(money) {

        if (typeof money != 'number') {
            return money;
        }
        var rs = money * 100;
        return rs ? rs : 0;
    },

    //时间格式化
    formatDate: function(v, f) {
        var F = f.replace(/\W/g, ',').split(','),
            format = ['yyyy', 'MM', 'dd', 'hh', 'mm', 'ss', 'ww'];
        var month = v.getMonth() + 1;
        if (month < 10) {
            month = '0' + month;
        }
        var dateStr = v.getDate();
        if (dateStr < 10) {
            dateStr = '0' + dateStr;
        }
        var date = {
            y: v.getFullYear(),
            M: month,
            d: dateStr,
            h: v.getHours(),
            m: v.getMinutes(),
            s: v.getSeconds(),
            w: v.getDay()
        };
        for (var i = 0, num = F.length; i < num; i++) {
            var o = F[i];
            for (var j = 0; j < 7; j++) {
                var S = format[j].slice(-1);
                if (o.match(S)) {
                    if (S == 'w' && date[S] == 0) date[S] = 7;
                    if (o.match(format[j])) {
                        f = f.replace(RegExp(format[j], 'g'), date[S]);
                    } else f = f.replace(RegExp(format[j].slice(format[j].length / 2), 'g'), date[S]);
                }
            }
        }
        return f;
    },

    //星期
    formatWeek(v) {
        return ['日', '一', '二', '三', '四', '五', '六'][v] || '';
    },

    //移除左右空格
    removeBlanks(str) {
        return str.replace(/(^\s*)|(\s*$)/g, "");
    },

    createQuery(obj) {
        var result = [];
        Object.keys(obj).forEach(function(key) {
            result.push(key + '=' + encodeURIComponent(obj[key]));
        });
        return result.join('&');
    },
    
    /**
     * 时间秒数格式化
     * @param msd 时间戳（单位：毫秒）
     * @returns {*} 格式化后的时分秒
     */
    MillisecondToTime(msd) {
        var s = msd;
        var t;
        if(s > -1){
            var hour = Math.floor(s/3600);
            var min = Math.floor(s/60) % 60;
            var sec = s % 60;
            if(hour < 10) {
                t = '0'+ hour + ":";
            } else {
                t = hour + ":";
            }

            if(min < 10){t += "0";}
            t += min + ":";
            if(sec < 10){t += "0";}
            t += Math.floor(sec);//.toFixed(2);
        }
        return t;
    },

    //获取字符的长度（字节单位）
    getStrByByteLength(str){

        return str.replace(/[^\x00-\xff]/g, 'xx').length;
    },

    //传入图片路径，返回base64 
    getBase64(img){

        function getBase64Image(img) {

          var canvas = document.createElement("canvas");
          canvas.width = img.width;
          canvas.height = img.height;
 
          var ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
          var dataURL = canvas.toDataURL();

          return dataURL;
        }

        return new Promise((rs, rj) => {

            var image = new Image();
            image.crossOrigin = 'anonymous';
            image.src = img;

            console.log(image);

            if(img){
                image.onload =function (){
                    rs(getBase64Image(image));
                }
            }
        });
    }
};


