<template>
	<section>
		<!-- 人员监控 -->
		<div>
			<div class="tis" v-show=" data.forecastInfo.need == 0">
				<p><span class="warning-tis warning"> 忙碌</span></p>
				<p><span class="normal-tis normal"> 空闲</span></p>
			</div>

			<div :class="'msg-box  ' + (data.forecastInfo.need > 0 ?  'warning' : 'normal')" v-show="data.forecastInfo.need != 0">
				<i class="el-icon-bell top-tis"></i>
				{{data.forecastInfo.need}}
			</div>

			<h2>实际检测人数</h2>
			<h3 style="color:red">{{data.workUserInfo.work.length}}</h3>
			<ul class="staff-ul">
				<li v-for="item in data.workUserInfo.work" class="warning-tis warning">
					<span>{{item.userName}}</span>
					<span>{{timeDiff(item.timestamp)}}</span>
				</li>
			</ul>
			<div class="foot">
				<h2>空闲人数 {{data.workUserInfo.free.length}} </h2>
				
				<ul class="staff-ul">
					<li v-for="item in data.workUserInfo.free" class="normal-tis normal">
						<span>{{item.userName}}</span>
						<span>{{timeDiff(item.timestamp)}}</span>
					</li>
				</ul>
			</div>
		</div>

		<!-- 队列监控 -->
		<div>
			<h2>等待队长</h2>
			<h3 style="color:green">{{data.waitQueueInfo.count}}</h3>
			<ul class="normal-ul">
				<li v-for="(item,index) in totals" :key="index" ><span>{{item.name}}</span> <span>{{item.total}}</span></li>
			</ul>
		</div>

		<audio id="voice_tip" type="audio/mp3" controls="controls" style="display: none;"><source src="http://cdn.huishoubao.com.cn/boss/js/media/7872.mp3">
			您的浏览器不支持 audio
		</audio>

	</section>
</template>

<script>
	import util from '../../common/js/util'
    import detCommon from '../../common/js/detCommon'
    import { getMonitoringCenter } from '../../api/api'

    export default {
    	data(){
    		return {
    			data:{
    				time:'',
    				forecastInfo:{
    					need: 0
    				},
    				waitQueueInfo:{
    					toc: 0,
    					hjx: 0,
    					atm: 0,
    					count: 0
    				},
    				workUserInfo:{
    					all:[],
    					free:[],
    					work:[]
    				}
    			},
    			totals: []
    		}
    	},
    	mounted(){
    		this.init();
    		this.getData();
    	},
    	beforeDestroy(){
            clearInterval(window._watchHeartbeatsId);
        },
    	methods:{

    		init(){
    			var taht = this;
    			window._watchHeartbeatsId =  setInterval(function(){ 
                    taht.getData();
                }, 3000);
    		},

    		getData(){
    			
    			getMonitoringCenter({}).then(res => {
    				if(res.status){
    					this.data = res.data;
    					if(this.data.forecastInfo.need > 0){
    						this.playVoiceTrip();
    					}
    					let temData = [{
    						name: "换机侠",
    						total: this.data.waitQueueInfo.hjx
    					},{
    						name: "TOC",
    						total: this.data.waitQueueInfo.toc
    					},{
    						name: "ATM",
    						total: this.data.waitQueueInfo.atm
    					}];

    					temData = temData.sort(detCommon.compare("total")).reverse();
    					this.totals = temData;
    				}else{
    					this.$message.error(res.retinfo)
    				}
    			})
    		},

    		timeDiff(sTiem){
    			let dTime = ( +this.data.time ) - ( +sTiem );
    			return util.MillisecondToTime(dTime);
    		},

    		playVoiceTrip: function() {
                var dom = document.getElementById('voice_tip');
                dom.play();
            }
    	},
    	watch:{
    	}
    }

</script>

<style scoped>
	section{
		display: flex;
		flex-wrap: nowrap;
		justify-content: center;
	}

	section>div{
		width: 400px;
		background-color: #E4E7ED;
		border-radius: 5px;
		margin: 10px 20px;
		padding: 20px;
	}

	h2,h3{
		color: #909399;
		text-align: center;
	}

	.warning{
		color: red;
	}

	.normal{
		color: green;
	}

	.top-tis{
		position: absolute;
		top: 5px;
		left: 5px;
		font-size: 22px;
	}

	.tis>p{
		color: #606266;
		margin: 0px;
		display: flex;
		justify-content: flex-end;
	}

	.warning.msg-box{
		border:3px solid red;
	}

	.normal.msg-box{
		border:3px solid green;	
	}

	.msg-box{
		font-weight: bold;
		position: relative;
		text-align: center;
		line-height: 150px;
		font-size: 30px;
		border-radius: 8px;
		height: 150px;
	}

	ul{
		padding: 0 50px;
		display: flex;
		flex-wrap: wrap;
	}

	.normal-ul li{
		width: 100%;
		justify-content: space-between;
		display: flex;
		line-height: 35px;
		border-bottom: 1px solid #ccc;
	}

	.normal-ul li span{
		padding: 0 30px;
	}

	.staff-ul li{
		width: 130px;
		line-height: 30px;
	}

	.staff-ul li span{
		margin-left: 5px;
	} 

	.warning-tis:before,
	.normal-tis:before{
		height: 20px;
		content: "●";
	}

	.foot{
		margin-top: 40px;
		border-top: 3px dotted #fff;
	}
</style>


