<template>
    <div>
        <section v-show="!showAction">
            <el-container>
                <el-header style="height: 50px;">
                    <el-row type="flex" justify="space-between" class="head_row">
                        <el-col  class="lt-txt">
                            型号：{{currentWorkOrder.productName}}， 
                            检测工号：{{currentWorkOrder.detectId}},
                            IMEI:{{currentWorkOrder.imei}}
                        </el-col>
                        <el-col >
                            <span class="img-name">
                                {{(picViewer.index || 0) + 1}}/{{ ((currentWorkOrder._productInfo || {}).pictures || []).length}}，
                                {{getImgName((((currentWorkOrder._productInfo || {}).pictures || [])[(picViewer.index || 0)] || {}))}}
                            </span>
                            <span class="query-order">
                                <el-button type="text" @click="showAction = true">点击，查看检测单详情</el-button>
                            </span>
                        </el-col>
                        <el-col >
                            <el-button icon="fa fa-power-off" size="medium" @click="quitDet">退出检测</el-button>
                        </el-col>
                    </el-row>
                </el-header>
                <el-container>
                    <!-- 图片区 -->
                    <el-main>
                        <div id="carouselDom">
                            <img style="display:none;" v-for="(picture,index) in currentWorkOrder._productInfo.pictures" :key="picture.name" :src="picture.value" />
                        </div>
                    </el-main>
                    <!-- 检测选择区 -->
                    <el-aside width="400px">
                        <!-- 机况选择区 -->
                        <fieldset>
                            <legend>
                                <strong>机况检测</strong>
                                <el-tooltip class="item" effect="dark" content="黄色为用户选择，蓝色为审批选择，红点为最近审批选择" placement="top-start">
                                    <i class="el-icon-question"></i>
                                </el-tooltip>
                            </legend>
                            <div class="content">
                                <ul class='det-option'>
                                    <li v-for="question in ((currentWorkOrder._detResult || {}).options || [])" 
                                        :key="question.id" 
                                        v-show="filtrationOption(question)">
                                        <strong>{{question.name}}</strong>
                                        <ul class="det-option-btn det-border-btn">
                                            <li :style="{ cursor: (isReject == 1 ? 'no-drop' : 'pointer') }" 
                                                v-for="answer in question.item" 
                                                @click="selectedAnswer(question,answer)" 
                                                :key="answer.id" 
                                                :class="((isReject != '1' && answer.selected == '1') ? ' selected ' : ' ') + (answer.styleAtr || '')">
                                                {{answer.name}}
                                            </li>
                                        </ul>
                                    </li>
                                </ul>
                            </div>
                        </fieldset>
                        <!-- 图片驳回区 -->
                        <fieldset>
                            <legend><strong>图片驳回</strong></legend>
                            <div class="content imp-content">
                                <p class="imp-h-p">
                                    <label>选择驳回原因： </label>
                                    <el-tag closable 
                                            :key="index" 
                                            size="mini" 
                                            @close="setReject(item,null,'remove')" 
                                            v-for="(item,index) in (currentWorkOrder._detResult.pictures[getCurrentImgIndex()] || {reject:''}).reject.split('#')"
                                            v-show="item != ''" >
                                        {{item}}
                                    </el-tag>
                                </p>
                                <ul class="det-option-btn">
                                    <li v-for="item in rejectData" :key="item.reasonId" @click="setReject(item,null,'add')">{{item.reason}}</li>
                                    <li @click="customInput = !customInput">其他</li>
                                    <li v-show="customInput">
                                        <input type="text" v-model="customInputVal"/>
                                        <button @click="setReject({reason: customInputVal},null,'add'); customInputVal = '';">确定</button>
                                    </li>
                                </ul>
                            </div>
                        </fieldset>
                        <el-button type="primary"  :disabled="isSubmit" :loading="isSubmitLoad"  @click="onSubmit" style="width:100%;"> {{ isReject == '1' ? '提 交(驳回)' : '提 交'}} </el-button>
                        <el-progress v-show="isReject == '1'" :percentage="rejectProgress" :show-text="false" status="success"></el-progress>
                    </el-aside>
                </el-container>
            </el-container>
        </section>
        <section v-show="showAction">
            <el-row type="flex" justify="center" class="el-header">
                <el-col :span="10">
                    <el-button type="text" @click="showAction = false">点击返回检测</el-button>
                </el-col>
            </el-row>
            <ul_det_info :data="relateWorkOrder" :detId="detectId" :opType="1"></ul_det_info>
        </section>
    </div>
</template>

<script>

    import constant from '../../common/js/constant'
    import common from '../../common/js/common'
    import detCommon from '../../common/js/detCommon'
    import detDetails from '../../component/detDetails'
    import viewer from '../../component/viewer'
    import '../../assets/det.css'
    import { getDetectOrderInfo, getRejectReasons, detectionIsExpire, actionWaitingDetectStatus, actionDetectRecord, getRelatedOrder, waitingDetectOrderTips } from '../../api/api'


    export default {
        data(){
            return {
                currentWorkOrder:{
                    _detResult:{
                        options:[],
                        pictures:[]
                    },
                    _productInfo:{
                        options:[],
                        pictures:[]
                    }
                },
                detectId: '',
                rejectData:[],
                dataLoad: false,
                customInput: false,
                customInputVal: '',
                //相关联工单
                relateWorkOrder:[],
                heartbeats: 0,
                watchHeartbeatsId: 0,
                maxHeartBeats: constant.VALID_TIME,
                isSubmitLoad: false,
                showAction: false,
                picViewer: {},
                haveReadPic:[],
                opNode: '',
                notify: {},
                waitDetectOrderInfo:{
                    detect: 0,
                    interpreter: 0
                }
            }
        },
        mounted(){
            this.getRejectData();
            this.init();
            this.watchWork();
        },
        beforeDestroy(){
            this.opNode = 'route';
            this.notify.close && this.notify.close();
            this.stopWork();
        },
        methods:{ 

            //获取工单
            getWorkOrder(){
                return new Promise((rs ,rj) => {
                    this.dataLoad = true;
                    getDetectOrderInfo({}).then(res => {
                        this.dataLoad = false;
                        if(!res.status){
                            if(res.retcode == '000101001'){
                                this.quitDet();
                            }
                            this.$message.info(res.retinfo);
                        }else{
                            this.detectId = res.data.detectId;
                        }
                        rs(res);
                    });
                })
            },

            //获取相关联的工单
            getRelatedWorkOrder(){
                return new Promise((rs ,rj) => {
                    getRelatedOrder({detectId: this.detectId}).then(res => {
                        let data = [];
                        if(!res.status){
                            this.$message.error(res.retinfo);
                        }
                        rs(res);
                    })
                });
            },

            watchQueue(){
                waitingDetectOrderTips({}).then(res =>{
                    if(res.status){
                        this.waitDetectOrderInfo = res.data;
                    }
                })
            },

            updateTime(){
                this.heartbeats = Math.floor(new Date().getTime() /1000);
            },

            //操作监控
            watchWork(){

                var taht = this;
                taht.updateTime();

                //输入设备监控
                document.onmousemove = document.onclick = function(e){
                    taht.updateTime();
                }
                document.onkeydown = function(e){
                    taht.updateTime();

                    var e = event || window.event || arguments.callee.caller.arguments[0],
                        v = e.keyCode;
                    
                    if(v === 37){ taht.picViewer.prev(true); }
                    if(v === 39){ taht.picViewer.next(true); }
                    if(v === 38){
                        e.preventDefault();
                        taht.picViewer.zoom(taht.picViewer.options.zoomRatio, true);
                    }
                    if(v === 40){
                        e.preventDefault();
                        taht.picViewer.zoom(-taht.picViewer.options.zoomRatio, true);
                    }
                }

                //监控操作
                window._watchHeartbeatsId =  setInterval(function(){ 
                    let _currentTime = Math.floor(new Date().getTime() /1000),
                        stayTime = _currentTime - taht.heartbeats,
                        surplusTime = taht.maxHeartBeats - stayTime,
                        warningTime = Math.floor( taht.maxHeartBeats / 5 * 2 );

                    if( surplusTime <= 0){
                        taht.quitDet();
                    }
                }, 1000);

                //监控队列
                window._watchQueue = setInterval(function(){
                    taht.watchQueue();
                },3000)
            }, 

            //停止工作
            async stopWork(){
                clearInterval(window._watchHeartbeatsId);
                clearInterval(window._watchQueue);
                let cancleStatus = await this.cancelWork();
                if(!cancleStatus.status){
                    alert('回退订单失败: ' + cancleStatus.retinfo);
                }
            },

            //将检测单退回待检测状态
            cancelWork(){
                return new Promise((rs,rj) => {
                    actionWaitingDetectStatus({detectId: this.currentWorkOrder.detectId}).then(res => {
                        rs(res);
                    });
                })
            },

            //回到待检测页面
            quitDet(){
                this.$router.push({ path: '/CloudsDetectList' });
            },

            //初始化
            init(){
                this.haveReadPic = [];
                this.distributionWork();
            },

            loadViewer(){
                if(this.picViewer.imageData == null){
                    this.picViewer =  new viewer(document.getElementById('carouselDom'), detCommon.viewerConfig);
                }else{
                    this.picViewer.update();
                    (this.haveReadPic.indexOf(this.picViewer.index) === -1) && this.haveReadPic.push(val || 0);
                }
            },

            //分配工单
            async distributionWork(){

                let workOrder = await this.getWorkOrder();
                let relatResults = {};
                let taht = this;

                if(workOrder.status){
                    //获取同型号工单
                    relatResults = await this.getRelatedWorkOrder();
                }

                if(relatResults.status){
                    
                    let historyData = relatResults.data,
                        currentData = {};
                    //设置历史工单数据
                    detCommon.setHistoryData(historyData);
                    currentData = detCommon.fromDetResult(workOrder.data);
                    currentData._productInfo.pictures = detCommon.detPic(currentData._productInfo.pictures, +currentData.accessId);
                    this.currentWorkOrder = currentData;

                    this.$nextTick().then(function(){
                        taht.loadViewer();
                    });
                    //封装历史工单对象
                    historyData.map(item => {
                        return detCommon.fromDetResult(item);
                    });
                    this.relateWorkOrder = historyData; 
                }
            },

            //获取驳回原因
            getRejectData(){
                getRejectReasons({}).then(res => {
                    if(res.status){
                        this.rejectData = res.data;
                    }else{
                       this.$message.error(res.retinfo); 
                    }
                });
            },

            //如果无图片类型则按照索引驳回
            getCurrentImgIndex(type){
                let l = this.currentWorkOrder._productInfo.pictures,
                    i = 0;
                if(type == null && l.length != 0){
                    type = l[this.picViewer.index || 0].type;
                }

                i = this.currentWorkOrder._detResult.pictures.findIndex(t => { return t.type == type });

                i = type == null ? this.picViewer.index : i;
                return i;
            },

            //设置驳回原因
            setReject(item, type, opType){

                this.customInput = false;
                let index = this.getCurrentImgIndex(type),
                    t = this.currentWorkOrder._detResult.pictures[index],
                    r = t.reject == '' ? [] : t.reject.split('#');

                if(opType == 'add'){
                    (r.indexOf(item.reason) == -1) && r.push(item.reason);
                }
                if(opType == 'remove'){
                    r.splice(r.indexOf(item), 1);
                }
                t.reject = r.join('#');
            },

            //校验选项选择
            verifyOption(){

                if(this.isReject == 1) return true;
                
                let isPass = true;
                this.currentWorkOrder._detResult.options.forEach(item => {
                    if((item.item || []).find( a => { return a.selected == '1' }) == null ){
                        isPass = false;
                        return false;
                    }
                });
                return isPass;
            },

            //提交检测
            onSubmit(){

                if(this.isSubmitLoad){
                    this.$message.warning('请勿重复提交');
                    return false;
                }

                if(!this.verifyOption()){
                    this.$message.warning('请选择完整选项');
                    return false;
                }

                let detResult = {
                        options: this.isReject == '1' ? [] : this.currentWorkOrder._detResult.options,
                        pictures: this.isReject == '0' ? [] : this.currentWorkOrder._detResult.pictures
                    },
                    ids = detCommon.getSelectID(detResult.options),
                    para = {
                        detectId: this.detectId,
                        uniqueId: this.currentWorkOrder.uniqueId,
                        productId: this.currentWorkOrder.productId,
                        productName: this.currentWorkOrder.productName,
                        isReject: this.isReject,
                        detectionResult: JSON.stringify(detResult),
                        detResultId: JSON.stringify(ids),
                        imei: this.currentWorkOrder.imei,
                        inspector: common.getUserInfo().real_name || ''
                    }

                this.isSubmitLoad = true; 

                actionDetectRecord(para).then(res => {
                    this.isSubmitLoad = false;
                    if(res.status){
                        this.$message.success('提交成功，进入下一笔工单');
                        this.init();
                    }else{
                        this.$message.error(res.retinfo)
                    }
                })
            },

            //过滤选项
            filtrationOption(q){
                if(q.conftype == null) return true;
                return q.conftype == constant.OPTION_TYPE.CDT;
            },

            //选择检测选项
            selectedAnswer(q,a){

                if(this.isReject == 1) return false;
                q.item.forEach(item => {
                    item.selected = '0';
                });
                a.selected = '1';
            },

            //获取图片位置名称
            getImgName(o){
                if(o.type == null) return o.name || '--';
                return common.getImgStr(o.type);
            }
        },
        computed:{

            //是否驳回
            isReject(){
                let rs = 0;
                if(this.currentWorkOrder._detResult == null) return rs;
                this.currentWorkOrder._detResult.pictures.forEach(item => {
                    if( item.reject != '') rs = 1;
                });
                return rs; 
            },

            //是否可以提交
            isSubmit(){
                if(this.isReject == 1 ){
                    return !(this.rejectProgress == 100);
                } 

                //选择了全部选项
                return !this.verifyOption();
            },

            rejectProgress(){

                let count = this.currentWorkOrder._productInfo.pictures.length,
                    cCount = this.haveReadPic.length;

                if(count <= 0) return 0;

                return (cCount/count) * 100;
            },
        },
        components:{
            'ul_det_info': detDetails
        },
        watch:{
            'picViewer.index'(val){
                (this.haveReadPic.indexOf(val) === -1) && this.haveReadPic.push(val || 0);
            },
            'waitDetectOrderInfo.interpreter'(v){

                if(v > 0){
                    let that = this,
                        currentTime = Math.floor(new Date().getTime() / 1000),
                        differTime = currentTime - detCommon.closeTisTime;
                    
                    if( detCommon.closeTisTime == 0 || 
                        differTime >= constant.TISTIME ){

                        if(this.notify.visible == null || this.notify.visible == false){
                            this.notify = this.$notify({
                              title: '提示',
                              iconClass: 'el-icon-bell',
                              message: '<strong style="color:red;" >有新的翻译单</strong>',
                              dangerouslyUseHTMLString: true,
                              duration: 0,
                              onClose:function(){
                                if(that.opNode == '')
                                    detCommon.chageCloseTime();
                              }
                            });
                        }
                    }
                }

                if(v <= 0){
                    this.opNode = 'route';
                    this.notify.close && this.notify.close();
                }
            }
        }
    }
</script>

<style scope='scoped'> 
    
    .lt-txt{
        text-align: left;
    }

    .el-aside{
        padding: 0 0 0 10px;
    }

    .el-header {
        background-color: #DCDFE6;
        text-align: center;
        line-height: 50px;
    } 
    .el-tag{
        margin-right: 5px;
    } 

    .imp-h-p{
        line-height: 30px;
        border-bottom: 1px solid #ccc;
    }

    .el-tag{
        color: red;
        border:1px solid red;
        background-color:#fff;
    }

    .head_row .query-order{
        display: none;
    }

    .head_row:hover .img-name{
        display: none;
    }

    .head_row:hover .query-order{
        display: inherit;
    }
</style>


