import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'

import CloudsDetectList from './views/detect/CloudsDetectList.vue'
import CloudsDetectDetail from './views/detect/CloudsDetectDetail.vue'
import CloudsDetection from './views/detect/CloudsDetection.vue'
import AccessConfigurationList from './views/detect/AccessConfigurationList.vue'
import DetectRecord from './views/detect/DetectRecord.vue'

import CloudsDiagram from './views/detect/CloudsDiagram.vue'
import CloudsDiagramList from './views/detect/CloudsDiagramList.vue'

import StaffWatch from './views/watch/StaffWatch.vue'

import common from '../src/common/js/common'

//存储路由信息
var RoutesMap = new Map();
RoutesMap.set('CloudsDetectList', CloudsDetectList);
RoutesMap.set('CloudsDetection', CloudsDetection);
RoutesMap.set('AccessConfigurationList', AccessConfigurationList);
RoutesMap.set('CloudsDetectDetail', CloudsDetectDetail);
RoutesMap.set('DetectRecord', DetectRecord);

RoutesMap.set('CloudsDiagram', CloudsDiagram);
RoutesMap.set('CloudsDiagramList', CloudsDiagramList);

RoutesMap.set('StaffWatch', StaffWatch);

let routes = [{
        path: '/login',
        component: Login,
        name: '',
        leaf: false,
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '欢迎页',
        hidden: true,
        leaf: false,
        children: [
            { path: '/Main', component: Main, name: '主页', hidden: true }
        ]
    }
];

const menu = common.getLoginUserMenu();

if (Array.isArray(menu)) {

    var routes_array = [];
    
    //根部
    menu.map(function(item, index) {
        //只有菜单权限才添加到导航中
        if (item.type != '2')
            return;

        var tree = {
            path: "/",
            component: Home,
            name: item.name,
            leaf: false
        };

        if (item.sub_menu != null && item.sub_menu.length > 0) {
            //叶子
            tree.children = [];
            item.sub_menu.map(function(citem, cindex) {
                var component = (citem.access_flags || '').substring(1);
                var leaf = {
                    path: citem.access_flags,
                    component: RoutesMap.get(component),
                    name: citem.name
                }
                tree.children.push(leaf);
                if (citem.sub_menu && citem.sub_menu.length) {
                    // 末级
                    citem.sub_menu.map(sitem => {
                        const component = (sitem.access_flags || '').substring(1);
                        const sub = {
                            path: sitem.access_flags,
                            component: RoutesMap.get(component),
                            name: sitem.name,
                            hidden: true
                        }
                        tree.children.push(sub);
                    })
                }
            });
        } else {
            tree.leaf = true;
            var component = (item.access_flags || '').substring(1);
            tree.children = [{
                path: item.access_flags,
                component: RoutesMap.get(component),
                name: item.name
            }];
        }
        routes.push(tree);
    });
}

routes.push({
    path: '*',
    hidden: true,
    leaf: false,
    component: NotFound
    // redirect: { path: '/404' }
});

export default routes;


