<!-- 接入配置信息 -->
<template>
    <div>
        <el-form label-position="right" :model="form" label-width="120px" v-loading="infoLoading">
            <fieldset>
                <legend>基本信息</legend>
                <div class="">
                    <el-form-item label="接入方名称">
                        <el-input v-model="form.accessName" placeholder="请输入内容"></el-input>
                    </el-form-item>
                    <el-form-item label="描述">
                        <el-input type="textarea" v-model="form.desc" autosize placeholder="请输入内容"></el-input>
                    </el-form-item>
                    <el-form-item label="接入ID类型">
                        <el-select v-model="form.typeId" placeholder="请选择">
                            <el-option
                                    v-for="item in accessTypes"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </div>
            </fieldset>

            <fieldset>
                <legend>检测单传入</legend>
                <div class="">
                    <el-form-item label="传入类型">
                        <el-checkbox-group v-model="form.incomingTypeArr">
                            <el-checkbox-button
                                    v-for="type in incomingTypes"
                                    :key="type.value"
                                    :label="type.label"
                                    :value="type.value">
                                {{type.label}}
                            </el-checkbox-button>
                        </el-checkbox-group>
                    </el-form-item>
                </div>
            </fieldset>

            <fieldset>
                <legend>检测配置</legend>
                <div class="">
                    <el-form-item label="主观类检测方式">
                        <!--<el-input v-model="form.subjectiveType" placeholder="请输入内容"></el-input>-->
                        <el-select v-model="form.subjectiveType" placeholder="请选择">
                            <el-option
                                    v-for="item in subjectiveTypes"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="客观类检测方式">
                        <el-select v-model="form.objectiveType" placeholder="请选择">
                            <el-option
                                    v-for="item in objectiveTypes"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="功能类检测方式">
                        <el-select v-model="form.functionType" placeholder="请选择">
                            <el-option
                                    v-for="item in functionTypes"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="是否需要估价">
                        <el-select v-model="form.isEvaluation" placeholder="请选择">
                            <el-option
                                    v-for="item in evaluationTypes"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </div>
            </fieldset>

            <div class="foot">
                <div class="foot-btn">
                    <el-button :type="actionType == 'info' ? 'primary' : ''" @click="close">取 消</el-button>
                    <el-button type="primary" @click="actionSubmit" v-show="actionType != 'info'">确 认</el-button>
                </div>
            </div>
        </el-form>
    </div>
</template>

<script type="text/javascript">

    import common from '../../../common/js/common'
    import { getAccessConfigInfo, setAccessConfigInfo } from '../../../api/api';

    const IncomingOptions       = [{label:'机型', value:'1'},{label:'IMEI', value:'2'},{label:'渠道ID', value:'4'}];
    const AccessTypeOptions     = [{label:'订单号', value:'1'},{label:'映射号', value:'2'}];
    const SubjectiveTypeOptions = [{label:'无需', value:'0'},{label:'人工检测', value:'1'},{label:'智能检测', value:'2'}];
    const ObjectiveTypeOptions  = [{label:'无需', value:'0'}];
    const FunctionTypeOptions   = [{label:'无需', value:'0'}];
    const EvaluationTypes       = [{label:'无需', value:'0'}];

    const _form = {
            accessName: '',
            desc: '',
            typeId: '',
            incomingType: '',
            incomingTypeArr: [],
            subjectiveType: '0',
            objectiveType: '0',
            functionType: '0',
            isEvaluation: '0',
        };

    export default {

        data(){
            return {
                incomingTypes: IncomingOptions,
                accessTypes: AccessTypeOptions,
                subjectiveTypes: SubjectiveTypeOptions,
                objectiveTypes: ObjectiveTypeOptions,
                functionTypes: FunctionTypeOptions,
                evaluationTypes: EvaluationTypes,

                form: { ..._form },

                btnStr:'确认',
                infoLoading: false,
            }
        },
        props:{
            // 1 新增/修改, 2 查看
            actionType:{
                type: String
            },
            accessId:{
                type:Number
            }
        },
        created() {
            this.loadData();
        },
        methods:{

            getIncomingTypeVal(){
                let incomingTypeVal = 0;
                this.form.incomingTypeArr.map(function(item){
                    IncomingOptions.map(function(option){
                        if (option.label == item) {
                            incomingTypeVal += parseInt(option.value);
                        }
                    });
                });
                return incomingTypeVal;
            },
            getIncomingTypeArr(value){
                var incomingTypeArr = [];
                for(var i = 0; (Math.pow(2, i)) <= value; i++){
                    if((Math.pow(2, i) & value) == Math.pow(2, i)){
                        IncomingOptions.map(function(option){
                            if (option.value == Math.pow(2,i)) {
                                incomingTypeArr.push(option.label);
                            }
                        });
                    }
                }
                return incomingTypeArr;
            },

            actionSubmit(){
                let para = Object.assign({}, this.form);

                if ( !para.accessName ) {
                    this.$message({ message: '请填写接入方名称', type: 'error' });
                    return false;
                } else if ( !para.typeId ) {
                    this.$message({ message: '请选择接入ID类型', type: 'error' });
                    return false;
                } else if ( !para.incomingTypeArr.length ) {
                    this.$message({ message: '请选择传入类型', type: 'error' });
                    return false;
                } else {
                    para.incomingType = this.getIncomingTypeVal();
                }

                delete para.incomingTypeArr;

                setAccessConfigInfo(para).then((res) => {
                    if(res.status){
                        this.$message.success('操作成功');
                    }else{
                        this.$message.error('操作失败：' + res.retinfo);
                    }
                    this.close();
                    window.location.reload();
                },(er) => {
                    this.listLoading = false;
                    console.log(er);
                });

            },

            initForm(){
                this.form = Object.assign(this.form, _form);
            },

            close(){
                this.initForm();
                this.$emit('notice', false);
            },

            loadData(){

                let para = {
                    accessId: this.accessId
                };

                if (para.accessId) {
                    this.infoLoading = true;
                    getAccessConfigInfo(para).then(res => {
                        this.infoLoading = false;
                        if (res.status) {
                            this.form = Object.assign(this.form, res.data);
                            this.form.incomingTypeArr = this.getIncomingTypeArr(this.form.incomingType);
                        } else {
                            this.$message.error(res.retinfo);
                        }
                    });
                } else {
                    this.initForm();
                }

            }
        },
        watch:{
            'accessId'(val){
                if(val > 0){
                    this.loadData();
                }
            }
        }
    }
</script>

<style type="text/css">

</style>



