<!-- 检测详情 -->
<template>
	<section>
		
		<!-- 进度 -->
		<fieldset>
			<legend><strong>机械检测进度</strong></legend>
			<div class="content" style="margin: 0 auto; position: relative; overflow: hidden;">
				<div class="el-steps el-steps--horizontal" style="overflow-x: auto;">
					<div class="el-step is-horizontal is-center" style=" position: relative;  vertical-align: middle;  width:170px; margin-right: 0px;" @click="setActive(index)"  v-for="(item , index) in stepData">
						<div class="el-step__head tips"><i  v-if="item.detectId != null && item.detectId == detId" class="el-icon-caret-bottom"></i></div>
						<div :class="'el-step__head ' + (active == index ? 'is-finish' : '')">
							<div class="el-step__line" style="margin-right: 0px;">
								<i class="el-step__line-inner" style="transition-delay: 0ms; border-width: 1px; width: 100%;"></i>
							</div>
							<div class="el-step__icon is-text">
								<div class="el-step__icon-inner">{{index+1}}</div>
							</div>
						</div>
						<div class="el-step__main">
							<div :class="'el-step__title ' + (active == index ? 'is-finish' : '')">{{item.status}}</div>
							<div :class="'el-step__description ' + (active == index ? 'is-finish' : '')">
								<p v-show="item.inspector != ''">{{item.inspector}}</p>
								<p v-show="item.time != ''">{{item.time}}</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</fieldset>
		
		<!-- <div v-for="(item,index) in (detData)" :key="item.detectId" v-show="index == active"> -->
			<!-- 检测单基本信息 -->
			<fieldset>
				<legend><strong>检测单信息</strong></legend>
				<div class="content">
					<ul class="cw-ul li-text">
						<li><label>检测ID: </label><span>{{showItem.uniqueId}}</span></li>
						<li><label>接入方: </label><span>{{showItem.accessName}}</span></li>
						<li><label>设备型号: </label><span>{{showItem.productName}}</span></li>
						<li><label>IMEI: </label><span>{{showItem.imei}}</span></li>
						<li><label>当前状态: </label><span>{{getStatus(showItem.status)}}</span></li>
						
					</ul>
				</div>
			</fieldset>

			<!-- 工单基本信息 -->
			<fieldset>
				<legend><strong>工单基本信息</strong></legend>
				<div class="content">
					<ul class="cw-ul li-text">
						<li><label>工单ID: </label><span>{{showItem.detectId}}</span></li>
						<li><label>检测工程师: </label><span>{{showItem.inspector}}</span></li>
						<li><label>检测时间: </label><span>{{showItem.detectTime}}</span></li>
						<li><label>等待时长: </label><span>{{fromSeconds(showItem.wattingTime)}}</span></li>
						<li><label>检测时长: </label><span>{{fromSeconds(showItem.duringTime)}}</span></li>
						<li><label>生成时间: </label><span>{{showItem.createTime}}</span></li>
					</ul>
				</div>
			</fieldset>

			<!-- 图片 -->
			<fieldset>
				<legend><strong>图片</strong></legend>
				<div class="content">
					<ul class="cw-ul det-img" id="infoPicContainer">
						<li v-for="pic in ((showItem || {})._detResult || {}).pictures">
							<p>
								<img class="viewer_pic" :src="pic.value || ''" title="点击查看大图" />
							</p>
							<p>{{getImgName(pic)}}</p>
							<p v-if="pic.reject != null && pic.reject != ''">
								<label class="sign">驳回</label>
								<span class="red-text" style="white-space:normal; ">{{pic.reject.replace(/#/g,' ') || ''}}</span>
							</p>
						</li>
					</ul>
				</div>
			</fieldset>

			<!-- 机况 -->
			<fieldset>
				<legend>
					<strong>机况</strong>
					<el-tooltip class="item" effect="dark" content="黄色为用户选择，蓝色为审批选择，红点为最近审批选择" placement="top-start">
						<i class="el-icon-question"></i>
					</el-tooltip>
				</legend>
				<div class="content">
					<ul class='det-option'>
                        <li v-for="(question, qIndex) in ((showItem || {})._detResult || {}).options" :key="'question_' + qIndex">
                            <strong>{{question.name}}</strong>
                            <ul class="det-option-btn det-border-btn">
                                <li v-for="(answer, aIndex) in question.item" 
                                	:key="'answer_' + qIndex + '_' + aIndex" 
                                	:class="(answer.selected == '1' ? ' selected ' : ' ') + (answer.styleAtr || '')">
                                    {{answer.name}}
                                </li>
                            </ul>
                        </li>
                    </ul>
				</div>
			</fieldset>
		<!-- </div> -->

	</section>
</template>

<script>
	import '../../../assets/det.css'
	import common from '../../../common/js/common'
	import util from '../../../common/js/util'
	import viewer from '../../viewer'

	export default {
		name: 'det_details',
		data(){
			return {
				cropperImg:{},
				detData:[],
				stepData:[],
				active: -1,
				imgVisible: false,
				imgBase64:'',
				imgLoad: false,
				picViewer: null,

				showItem: {}
			}
		},
		props:{
			data:{
				type:Array,
				default:[]
			},
			opType:{
				type: Number
			},
			detId:{
				type: String,
				default: ''
			}
		},
		mounted(){
		},
		methods:{

			formatterData(){
				this.active = 0;
				this.detData = this.data;
				
				this.getSteps();
				this.setShowItem();
			},


			setShowItem(){
				let taht = this;
				this.showItem = this.detData[this.active];
				this.$nextTick().then(function(){
                    taht.loadViewer();
                });
			},

			getSteps(){
				let list = [];
				let data = this.detData;

				if(this.opType == '1'){
					data.map(item => {
						let o = {
							detectId: item.detectId, 
							status: common.getStatusStr(item.status),
							inspector: item.inspector || '',
							time: item.updateTime || '',
							ioc: 'el-icon-setting'
						};
						list.push(o);
					})
				}

				if(this.opType == '2'){
					(data[0].actionList || []).map(item => {
						let o = {
							status: common.getStatusStr(item.status),
							actionTime: item.createTime,
							time: item.createTime,
							inspector: item.userName,
							ioc: 'el-icon-setting'
						};
						list.push(o);
					})
				}

				list.map(item => {
					if(item.status == '3') item.ioc = 'el-icon-success';
					if(item.status == '4') item.ioc = 'el-icon-picture'; 
					if(item.status == '5') item.ioc = 'el-icon-circle-close';
					return item;
				});
				this.stepData = list;
			},

			setActive(index){
				if(this.opType == '1') this.active = index;
			},


			loadViewer(){

				if(this.picViewer == null){
                    this.picViewer =  new viewer(document.getElementById('infoPicContainer'),{
                        title: false,
                        tooltip: false,
                        fullscreen: false,

                        toolbar:{
                            zoomIn: true,
                            zoomOut: true,
                            prev: true,
                            next: true,
                            reset: true,
                            rotateLeft: true,
                            rotateRight: true,
                            play:false
                        }
                    });
                }else{
                    this.picViewer.update();
                }
			},

			getStatus(v){
				return common.getStatusStr(v);
			},

			fromSeconds(v){
				return common.secondFormat(v);
			},

			//获取图片位置名称
            getImgName(o){
                if(o.type == null) return o.name || '--';
                return common.getImgStr(o.type);
            }
		},
		watch:{
			'data'(val){
				if(val != null){
					this.formatterData();
				}
			},
			'active'(val){
				this.setShowItem();
			}
		},
		components: {
		
		}
	}
</script>

<style scope='scoped'>

	.el-step.is-center .el-step__description{
		padding: 0px;
	}

	.el-step.is-center .el-step__description p{
		margin: 0px;
		line-height: 25px;
	}

	.tips{
		height: 15px;
		color: red;
		font-size: 16px;
	}

	.det-option-btn li:hover{
        color: #000;
    }

</style>


