<template>
    <section>
        <el-container>
            <el-header style="height: 50px;">
                <el-row type="flex" justify="space-between" class="head_row">
                    <el-col :span="5">
                        型号：{{currentWorkOrder.productName}}， 
                        翻译工号：{{currentWorkOrder.detectId}}
                    </el-col>
                    <el-col :span="10">
                        {{(picViewer.index || 0) + 1}}/{{ currentWorkOrder._productInfo.pictures.length }}, 
                        {{ getImgName(currentWorkOrder._productInfo.pictures[picViewer.index || 0]) }}
                    </el-col>
                    <el-col :span="5">
                        <el-button icon="fa fa-power-off" size="medium" @click="quitDet">退出翻译</el-button>
                    </el-col>
                </el-row>
            </el-header>
            <el-container>
                <!-- 图片区 -->
                <el-main>
                    <div id="carouselDom">
                        <img style="display:none;" v-for="(picture,index) in currentWorkOrder._productInfo.pictures" :key="picture.name" :src="picture.value" />
                    </div>
                </el-main>
                <!-- 翻译选择区 -->
                <el-aside width="400px" v-loading="imeiQueryLoading">
                    <!-- 机况选择区 -->
                    <fieldset>
                        <legend>
                            <strong>翻译内容</strong>
                        </legend>
                        <div class="content">
                            <ul class='det-option'>
                                <li>
                                    <strong>IMEI</strong>
                                    <p>
                                        <!-- <el-input v-model="imei" size="mini" :maxlength="15" placeholder="请输入IMEI"></el-input> -->
                                        <multiple-input v-model="imei" :inputLength="15"></multiple-input>
                                        <section style="color:red;" v-show="imeiStatus == '3'">
                                            IMEI有误，请检测是否录入错误?
                                        </section>
                                        <section v-show="imeiStatus == '3'">
                                            <el-button size="mini" @click="notarizeIMEI">确认输入无误</el-button>
                                        </section>
                                    </p>
                                </li>
                                <li v-show="currentWorkOrder.phoneType == phoneSystem.Android">
                                    <strong>产品</strong>
                                    <p>
                                        <el-select
                                            v-model="productId"
                                            filterable
                                            remote
                                            size="mini"
                                            reserve-keyword
                                            placeholder="请输入机型关键词"
                                            :remote-method="queryProduct"
                                            :loading="loadingModel">
                                            <el-option
                                              v-for="item in productList"
                                              :key="item.productId"
                                              :label="item.productName"
                                              :value="item.productId">
                                            </el-option>
                                            <el-option key="notModel" label="找不到机型!" value="notModel"></el-option>
                                        </el-select>
                                        <el-input style="width:200px;" v-show="productId == 'notModel'" v-model="definedProduct" size="mini" placeholder="请输入您所看到的机型"></el-input>
                                    </p>
                                </li> 
                                <li v-for="question in currentWorkOrder._detResult.options" :key="question.id" v-if="question.conftype == 1">
                                    <strong>{{question.name}}</strong>
                                    <ul class="det-option-btn det-border-btn">
                                        <li :style="{ cursor: (isReject == 1 ? 'no-drop' : 'pointer') }" 
                                            v-for="answer in question.item" 
                                            @click="selectedAnswer(question,answer)" 
                                            :key="answer.id" 
                                            :class="((isReject != '1' && answer.selected == '1') ? ' selected ' : ' ')">
                                            {{answer.name}}
                                        </li>
                                    </ul>
                                </li>
                            </ul>
                        </div>
                    </fieldset>
                    <!-- 图片驳回区 -->
                    <fieldset>
                        <legend><strong>图片驳回</strong></legend>
                        <div class="content imp-content">
                            <p class="imp-h-p">
                                <label>选择驳回原因： </label>
                                <el-tag closable 
                                        :key="index" 
                                        size="mini" 
                                        @close="setReject(item,null,'remove')" 
                                        v-for="(item,index) in (currentWorkOrder._detResult.pictures[getCurrentImgIndex()] || {reject:''}).reject.split('#')"
                                        v-show="item != ''" >
                                    {{item}}
                                </el-tag>
                            </p>
                            <ul class="det-option-btn">
                                <li v-for="item in rejectData" :key="item.reasonId" @click="setReject(item,null,'add')">{{item.reason}}</li>
                                <li @click="customInput = !customInput">其他</li>
                                <li v-show="customInput">
                                    <input type="text" v-model="customInputVal"/>
                                    <button @click="setReject({reason: customInputVal},null,'add'); customInputVal = '';">确定</button>
                                </li>
                            </ul>
                        </div>
                    </fieldset>
                    <el-button type="primary" :disabled="isSubmit" :loading="isSubmitLoad" @click="onSubmit" style="width:100%;"> {{ isReject == '1' ? '提 交(驳回)' : '提 交'}} </el-button>
                    <el-progress v-show="isReject == '1'" :percentage="rejectProgress" :show-text="false" status="success"></el-progress>
                </el-aside>
            </el-container>
        </el-container>
    </section>
</template>

<script>

    import constant from '../../common/js/constant'
    import common from '../../common/js/common'
    import verify from '../../common/js/verify'
    import detCommon from '../../common/js/detCommon'
    import viewer from '../../component/viewer'
    import multipleInput from '../../component/multipleInput'

    import '../../assets/det.css'
    import { getDetectOrderInfo, getRejectReasons, actionWaitingInterpreterStatus, getProductList, getProductInfo, submitTranslate, getProductInfoByIMEI, getProdictModelByIMEI} from '../../api/api'

    export default {
        data(){
            return {
                imei: '',
                currentWorkOrder:{
                    _detResult:{
                        options:[],
                        pictures:[]
                    },
                    _productInfo:{
                        options:[],
                        pictures:[]
                    }
                },
                detectId: '',
                rejectData:[],
                rejectType: '',
                productId: '',
                productList: [],
                productOptions: [],

                customInputVal: '',
                loadingModel: false,
                customInput: false,
                heartbeats: 0,
                watchHeartbeatsId: 0,
                maxHeartBeats: 60,//constant.VALID_TIME,
                phoneSystem: constant.PHONE_SYSTEM,
                optionType: constant.OPTION_TYPE,
                isSubmitLoad: false,
                loading: false,
                imeiQueryLoading: false,
                definedProduct: '',
                //1: 未知，2：正确，3：错误
                imeiStatus: '1', 
                picViewer: {},
                thirdPartyInfo:[],
                //已阅图片
                haveReadPic:[]
            }
        },
        mounted(){ 
            this.getRejectData();
            this.init();
            this.watchWork();
        },
        beforeDestroy(){
            this.stopWork();
        },
        methods:{ 

            //获取工单
            getWorkOrder(){
                return new Promise((rs ,rj) => {
                    this.dataLoad = true;
                    getDetectOrderInfo({type: '2'}).then(res => {
                        this.dataLoad = false;
                        if(!res.status){
                            if(res.retcode == '000101001'){
                                this.quitDet();
                            }
                            this.$message.info(res.retinfo);
                        }else{
                            this.detectId = res.data.detectId;
                        }
                        rs(res);
                    });
                })
            },

            updateTime(){
                this.heartbeats = Math.floor(new Date().getTime() /1000);
            },

            //操作监控
            watchWork(){

                var taht = this;
                taht.updateTime();

                //输入设备监控
                document.onmousemove = document.onclick = function(e){ 
                    taht.updateTime();
                }
                document.onkeydown = function(e){
                    taht.updateTime();

                    var e = event || window.event || arguments.callee.caller.arguments[0],
                        v = e.keyCode;
                    
                    if(v === 37){ taht.picViewer.prev(true); }
                    if(v === 39){ taht.picViewer.next(true); }
                    if(v === 38){
                        e.preventDefault();
                        taht.picViewer.zoom(taht.picViewer.options.zoomRatio, true);
                    }
                    if(v === 40){
                        e.preventDefault();
                        taht.picViewer.zoom(-taht.picViewer.options.zoomRatio, true);
                    }
                }

                window._watchHeartbeatsId =  setInterval(function(){ 
                    let _currentTime = Math.floor(new Date().getTime() /1000),
                        stayTime = _currentTime - taht.heartbeats,
                        surplusTime = taht.maxHeartBeats - stayTime,
                        warningTime = Math.floor( taht.maxHeartBeats / 5 * 2 );

                    if( surplusTime <= 0){
                        taht.quitDet();
                    }
                }, 1000);
            }, 

            //停止工作
            async stopWork(){
                clearInterval(window._watchHeartbeatsId);
                let cancleStatus = await this.cancelWork();
                if(!cancleStatus.status){
                    alert('回退订单失败: ' + cancleStatus.retinfo);
                }
            },

            //将翻译单退回待翻译状态
            cancelWork(){
                return new Promise((rs,rj) => {
                    actionWaitingInterpreterStatus({detectId: this.currentWorkOrder.detectId}).then(res => {
                        rs(res);
                    });
                })
            },

            //回到列表页面
            quitDet(){
                this.$router.push({ path: '/CloudsDiagramList' });
            },

            //根据产品名称模糊查询产品
            async queryProduct(search){

                if(search == ''){ this.productList = []; return false; } 

                this.loadingModel = true;
                let queryRs = await this.getProductListByName(search);
                this.loadingModel = false;

                if(queryRs.status){
                    this.productList = queryRs.data.pdtList;
                }else{
                    this.productList = [];
                    this.$message.error(queryRs.retinfo);
                }
            },

            getProductListByName(productName){
                
                return new Promise((rs, rj) => {
                    getProductList({channelId: this.currentWorkOrder.channelId, search: productName}).then(res => {
                        rs(res);
                    });      
                });
            },

            //通过IMEI 获取产品信息
            //1, 通过第三方服务获取设备信息
            //2，通过设备名称获取产品信息
            //3，获取产品选项
            //4，将第三方查询结果映射并且选择到产品选项上
            async getProdictByIMEI(){

                this.imeiQueryLoading = true;

                //第三方查询结果
                let tRs = await this.getThirdPartyInfo(),
                    _tpInfo = [];
                if(tRs.status){
                    _tpInfo = detCommon.mappingOptions(tRs.data);
                }
                if(_tpInfo.length == 0){
                    this.imeiStatus = '3';
                    this.$message.error('IMEI查询不到结果')
                    this.imeiQueryLoading = false;
                    return false;   
                }
                let lockRs = await this.getThirdPartyLockInfo(),
                    _lockInfo = [];
                if(lockRs.status){
                    _lockInfo = detCommon.mappingOptions(lockRs.data);
                }
                if(_lockInfo.length == 0){
                    this.imeiStatus = '3';
                    this.$message.error('IMEI查询ID锁失败');
                    this.imeiQueryLoading = false;
                    return false;   
                }

                //第三方查询结果合并
                this.thirdPartyInfo = _tpInfo.concat(_lockInfo);

                //获取产品信息
                let pName = tRs.data.name;
                let qRs = await this.getProductListByName(pName);
                if(!qRs.status){
                    this.imeiStatus = '3';
                    this.$message.error('查询产品失败，' + qRs.retinfo);
                    this.imeiQueryLoading = false;
                    return false;
                }
                let plist = qRs.data.pdtList,
                    p = plist.filter(item => { return item.productName === pName });
                if(p.length != 1){
                    this.$message.warning(pName + '搜索失败，相关产品数量：' + p.length);
                    this.imeiStatus = '3';
                    this.imeiQueryLoading = false;
                    return false;
                }
                this.productList = p;
                let currentProduct = p [0];
                this.productId = currentProduct.productId;
                this.productLogo = currentProduct.productLogo;
                this.imeiStatus = '2';
                this.imeiQueryLoading = false;
            },

            //获取第三方信息
            getThirdPartyInfo(){
                return new Promise((rs, rj) => { 
                    getProductInfoByIMEI({imei: this.imei}).then(res => {
                        rs(res);
                    });
                });
            },

            getThirdPartyLockInfo(){
                return new Promise((rs, rj) => {
                    getProdictModelByIMEI({imei: this.imei}).then(res => {
                        rs(res);
                    });
                });
            },

            //获取产品选项
            getProductOption(){
                getProductInfo({channelId: this.currentWorkOrder.channelId, productId: this.productId}).then(res => {
                    if(res.status){

                        var productOptions = detCommon.formattingOptions(res.data.itemList);
                        this.currentWorkOrder._productInfo.options = productOptions;

                        var temData = detCommon.formOptions(productOptions);
                        //选项分类
                        temData = detCommon.dataGrouping(temData, +this.currentWorkOrder.accessId);
                        
                        //选择第三方检测选项
                        temData = detCommon.setDefaultSelectByThirdPartyInfo(temData, this.thirdPartyInfo);

                        //功能项默认最好的
                        temData = detCommon.setDefaultSelect(temData, constant.OPTION_TYPE.FUN);

                        if(this.currentWorkOrder.phoneType == constant.PHONE_SYSTEM.IOS){
                            //未选择的SKU项默认选择最好的
                            temData = detCommon.setDefaultSelect(temData, constant.OPTION_TYPE.SKU);
                        }
                        this.currentWorkOrder._detResult.options = temData; 
                    }else{
                        this.$message.error(res.retinfo);
                    }
                });
            },

            //初始化
            init(){
                this.imei = '';
                this.productId = '';
                this.productList = [];
                this.haveReadPic = [];
                this.distributionWork();
            },

            //加载图片交互组件
            loadViewer(){
                if(this.picViewer.imageData == null){
                    this.picViewer =  new viewer(document.getElementById('carouselDom'), detCommon.viewerConfig);
                }else{
                    this.picViewer.update();
                    (this.haveReadPic.indexOf(this.picViewer.index) === -1) && this.haveReadPic.push(val || 0);
                }
            },

            //分配工单
            async distributionWork(){

                let workOrder = await this.getWorkOrder(),
                    taht = this,
                    temWorkOrder = {};

                if(!workOrder.status) return false;

                temWorkOrder = detCommon.fromDetResult(workOrder.data, +workOrder.data.accessId); 
                temWorkOrder._productInfo.pictures = detCommon.translatePic(temWorkOrder._productInfo.pictures, +temWorkOrder.accessId);

                this.currentWorkOrder = temWorkOrder;

                this.$nextTick().then(function(){
                    taht.loadViewer();
                });
            },

            //获取驳回原因
            getRejectData(){
                getRejectReasons({type: "2"}).then(res => {
                    if(res.status){
                        this.rejectData = res.data;
                    }else{
                       this.$message.error(res.retinfo); 
                    }
                });
            },

            getCurrentImgIndex(type){

                let l = this.currentWorkOrder._productInfo.pictures,
                    i = 0;

                if(type == null && l.length != 0){
                    type = l[this.picViewer.index || 0].type;
                }
                i = this.currentWorkOrder._detResult.pictures.findIndex(t => { return t.type == type });
                return i;
            },

            //设置驳回原因
            setReject(item, type, opType){

                this.customInput = false;
                let index = this.getCurrentImgIndex(type),
                    t = this.currentWorkOrder._detResult.pictures[index],
                    r = t.reject == '' ? [] : t.reject.split('#');

                if(opType == 'add'){
                    (r.indexOf(item.reason) == -1) && r.push(item.reason);
                }
                if(opType == 'remove'){
                    r.splice(r.indexOf(item), 1);
                }
                t.reject = r.join('#');
            },

            removeReject(item){
                let index = this.getCurrentImgType(),
                    img = this.currentWorkOrder._detResult.pictures.find(t => { return t.type == type }),
                    list = img.reject.split('#');
                list.splice(list.indexOf(item), 1);
                img.reject = list.join('#');
            },

            //校验选项选择
            verifyOption(){

                if(this.isReject == 1) return true;

                let isPass = true;

                if(this.currentWorkOrder._detResult == null || this.currentWorkOrder._detResult.options.length == 0) return false;
                
                this.currentWorkOrder._detResult.options.forEach(item => {
                    
                    if(item.conftype != this.optionType.CDT){
                        let selectItem = (item.item || []).find( a => { return a.selected == '1' });
                        if(selectItem == null ){
                            isPass = false;
                            return false;
                        }
                    }
                });
                return isPass;
            },

            //提交检测
            onSubmit(){

                if(this.isSubmitLoad){
                    this.$message.warning('请勿重复提交数据');
                    return false;
                }

                if(!this.verifyOption()){
                    this.$message.warning('请选择完整选项');
                    return false;
                }

                //功能类默认选中                
                let detResult = {
                        options: this.isReject == '1' ? [] : this.currentWorkOrder._detResult.options,
                        pictures: this.isReject == '0' ? [] : this.currentWorkOrder._detResult.pictures
                    },
                    currentProduct = this.productList.find(item => { return item.productId == this.productId }) || {},
                    ids = detCommon.getSelectID(detResult.options),
                    para = {
                        detectId: this.detectId,
                        uniqueId: this.currentWorkOrder.uniqueId,
                        productId: this.productId,
                        productOptions: JSON.stringify(this.currentWorkOrder._productInfo.options),
                        productName: currentProduct.productName || '',
                        productLogo: currentProduct.productLogo || '',
                        isReject: this.isReject,
                        rejectType: this.rejectType,
                        detectionResult: JSON.stringify(detResult),
                        imei: this.imei,
                        inspector: common.getUserInfo().real_name || '',
                        detResultId: JSON.stringify(ids)
                    };
                this.isSubmitLoad = true;

                submitTranslate(para).then(res => {
                    this.isSubmitLoad = false;
                    if(res.status){
                        this.$message.success('提交成功，进入下一笔工单');
                        this.init();
                    }else{
                        this.$message.error(res.retinfo);
                    }
                });
            },

            //选择检测选项
            selectedAnswer(q,a){
                if(this.currentWorkOrder.phoneType == constant.PHONE_SYSTEM.IOS) return false;
                if(this.isReject == 1) return false;
                q.item.forEach(item => {
                    item.selected = '0';
                });
                a.selected = '1';
            },

            notarizeIMEI(){
                this.$confirm('确认IMEI与图片输入一致吗?', '确认', {
                    confirmButtonText: '在检查下',
                    cancelButtonText: '是的，确认一致',
                    type: 'warning'
                }).then(() => {}).catch(() => {
                    this.rejectIMEI();   
                });
            },

            //获取图片位置名称
            getImgName(o){
                if(o == null || o.type == null) return '--';
                return common.getImgStr(o.type);
            },

            //驳回IMEI照片
            rejectIMEI(){

                this.setReject({reason:'IMEI错误'}, constant.IMG_TYPE.IMEI, 'add');
                this.onSubmit();
            }
        },
        computed:{

            //是否驳回
            isReject(){

                let rs = 0;
                this.rejectType = 0;

                //如果选择找不到机型驳回类型则为暂无机型驳回
                if(this.productId == 'notModel'){
                    this.rejectType = constant.REJECT_TYPE.NOT_PRODUCT;
                    rs = 1;
                }

                //如果图片选择了驳回原因则为图片驳回
                let detResult = this.currentWorkOrder._detResult;
                if(detResult == null) return rs;
                for (var i = detResult.pictures.length - 1; i >= 0; i--) {
                    if(detResult.pictures[i].reject != ''){
                        rs = 1;
                        this.rejectType = constant.REJECT_TYPE.IMG;        
                        break;
                    }
                }
                return rs; 
            },

            rejectProgress(){

                let count = this.currentWorkOrder._productInfo.pictures.length,
                    cCount = this.haveReadPic.length;

                if(count <= 0) return 0;

                return (cCount/count) * 100;
            },

            //是否可以提交产品
            isSubmit(){

                if(this.isReject == 1 && this.rejectProgress == 100 ) return false;

                let detOption = this.verifyOption(),
                    ckImei = false;

                if(this.currentWorkOrder.phoneType == constant.PHONE_SYSTEM.IOS){
                    ckImei = verify.imei(this.imei);
                }
                if(this.currentWorkOrder.phoneType == constant.PHONE_SYSTEM.Android){
                    ckImei = this.imei != '';
                }
                if(ckImei === false || detOption === false) return true;
                return false;
            }
        },
        components:{
            'multiple-input': multipleInput
        },
        watch:{
            'productId'(val){
                if(val != '' && val != 'notModel'){
                    this.getProductOption();
                }
            },
            'imei'(val){
                if( this.currentWorkOrder.phoneType == constant.PHONE_SYSTEM.IOS && verify.imei(val)){
                    this.getProdictByIMEI();
                }else{
                    this.imeiStatus = "2";
                }
            },
            'picViewer.index'(val){ 
                (this.haveReadPic.indexOf(val) === -1) && this.haveReadPic.push(val || 0);
            }
        }
    }
</script>

<style scope='scoped'> 
    
    .row-title{
        width: 80px;
        padding: 0 10px;
    }

    .mini-input-cn{
        width: 100%;
    }

    .mini-input-cn input[type="number"],
    .mini-input-cn input[type="text"],
    .mini-input{
        width: 25px;
        height: 25px;
        text-align: center;
        margin-right: 3px;
        margin-bottom: 5px;
    }

    .el-main{
        min-height: 700px;
    }

    .el-aside{
        padding: 0 0 0 10px;
    }

    .el-header {
        background-color: #DCDFE6;
        text-align: center;
        line-height: 50px;
    } 
    .el-tag{
        margin-right: 5px;
    } 

    .imp-h-p{
        line-height: 30px;
        border-bottom: 1px solid #ccc;
    }

    .el-tag{
        color: red;
        border:1px solid red;
        background-color:#fff;
    } 
</style>


