<template>
	<section id="imei_input">
		<input v-for="(item,index) in inputLength" :class="(index == (inputLength-1)) ? ' last' : ''" type="text" maxlength="1" v-model="inputList.values[index]" @blur="setValues" />
	</section>
</template>

<script>
	export default {
		name: 'det_multipleInput',
		data(){
			return {
				inputList:{
					values:[]
				}
			}
		},
		props:['value','inputLength'],
		mounted(){

			let c = document.getElementById('imei_input'),
			 	inputs = c.getElementsByTagName("input");

			for (var i = 0; i < inputs.length; i++) {
				let t = inputs[i];
				t.index = i;
	            t.onkeyup = function() {

	            	let str = this.value;
	            	str = str.replace(/[^a-z][^0-9]/ig,""); 

	                this.value = str;
	                var next = this.index + 1;

	                if(str == '') return;
	                if(next > (inputs.length -1)){
	                	this.blur();
	                	return;
	                }
	                inputs[next].focus();
	            }
			}
		},
		methods: {
			setValues(){
				let inputs = this.inputList.values;
				this.$emit('input', inputs.join(''));
			},
			clearValues(){
				let c = document.getElementById('imei_input'),
			 		inputs = c.getElementsByTagName("input");

			 	for (var i = 0; i < inputs.length; i++) {
			 		inputs[i].value = '';
			 	}
			}
		},
		watch:{
			'value'(val){
				if(val == ''){
					this.clearValues();
				}
			}
		}
	}
</script>

<style scoped>
	
	section input[type="text"] {
		width: 18px;
		height: 20px;
		text-align: center;
		border: 1px solid #ccc;
		border-right: 0px;
        -moz-appearance: textfield;
    }

    section input[type="text"].last{
    	border-right: 1px solid #ccc;
    }

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none !important;
        margin: 0;
    }
</style>


